namespace fsync {

	export type PrefabId = string

	export class PrefabMeta {
		prefabId: PrefabId

		init(prefabId: PrefabId) {
			this.prefabId = prefabId
			return this
		}
	}

	export interface PrefabEnv {
		system?: fsync.SystemBase
		entityManager: fsync.EntityManager
		world: fsync.ECSWorld
	}

	/**
	 * prefab
	 */
	export class ViewPrefabEnv {
		entityManager: EntityManager
		viewBinder: ViewBindManager
		prefabHelper: IPrefabHelper
		utils: FrameSyncUtils
		init(world: ECSWorld, viewBinder: ViewBindManager, prefabHelper: IPrefabHelper) {
			this.utils = world.utils
			this.entityManager = world.entityManager
			this.viewBinder = viewBinder
			this.prefabHelper = prefabHelper
			return this
		}

		instantiate(prefab: IPrefab): Entity {
			return this.prefabHelper.instantiate(prefab, this)
		}
	}

	export interface IPrefab {
		init(): IPrefab
		load(): IPrefab
		createEntity(depsEnv: ViewPrefabEnv): Entity
		getPrefabMeta(): PrefabMeta
		create(depsEnv: ViewPrefabEnv): Entity
	}

}