
namespace fsync.ecsproxy {

	/**
	 * 
	 */
	export class ProxyClasses {
		compProxies: (new () => IComponent)[] = []
		entityProxies: (new () => EntityProxyBase)[] = []

		decorated: bool = false

		autoDecorate: bool = true

		init() {
			/**
			 *  decorateAll()
			 * - ,
			 */
			// setTimeout(() => {
			// 	if (this.autoDecorate) {
			// 		this.decorateAll()
			// 	}
			// }, 1)

			return this
		}

		/**
		 * ,
		 */
		decorateAll() {
			if (this.decorated) {
				return
			}

			for (let proxyClass of this.compProxies) {
				DoDecoCompProxy(proxyClass)
			}
			for (let proxyClass of this.entityProxies) {
				DoDecoEntityProxy(proxyClass)
			}

			StandCompConfig = StandCompConfigSimple
			StandCompProxy = StandCompProxySimple
			StandEntityProxy = StandEntityProxySimple
			StandEntityProxyArray = StandEntityProxyArraySimple
			StandCustomData = StandCustomDataSimple
			StandCustomDataArray = StandCustomDataArraySimple

			this.decorated = true

		}
	}

	/**
	 * 
	 */
	export const proxyClasses = new ProxyClasses().init()

}
