namespace fsync.ecsproxy {
	import IEntityQuery = fsync.IEntityQuery;

	export class EntityProxyArray<T extends EntityProxyBase> extends Array<T> {
		__key: string
		__host: IECSComponentAttrArrayProxyBase
		__cls: new () => T

		push(...items: T[]): number {
			const host = this.__host
			const comp = host.__comp
			const entityManager = host.__entityManager
			const cls = this.__cls
			let ids = comp[this.__key] as string[]

			for (let i = 0; i < items.length; i++) {
				let item = items[i]
				let id: string
				if (typeof (item) == "object") {
					if (item instanceof Entity) {
						id = item.identity
					} else {
						id = item.entityId
					}
				} else if (typeof (item) == "string") {
					id = item
				} else {
					console.error("invalid type")
				}
				ids.push(id)
				const entity = entityManager.wrapEntityId(id)
				const proxy = entityProxyHelper.getEntityProxy(cls, entityManager, entity)
				super.push(proxy)
			}
			return this.length
		}
	}

}
