namespace fsync {

	export type ECSCommandFunc<T> = (...args: T[]) => void
	export type ECSCommand<T = any> = {
		call: ECSCommandFunc<T>
		paras: T[]
	}

	export class ECSCommandBuffer {
		protected calls: ECSCommand[]
		init() {
			this.calls = []
			return this
		}
		addCommond<T>(call: ECSCommandFunc<T>, ...paras: T[]) {
			this.calls.push({
				call,
				paras,
			})
		}
		run() {
			// console.log("ECSCommandBuffer.run", this.calls.length)
			for (let call of this.calls
				.concat()
			) {
				call.call(...call.paras)
			}
		}
		clearCommonds() {
			this.calls.length = 0
		}
	}


	// run on main
	export class SystemBase implements IUpdater {
		world: ECSWorld
		protected _commondBuffer: ECSCommandBuffer
		protected _commondBufferAfterUpdate: ECSCommandBuffer

		ctype: string = "unknown"

		get timer(): fsync.Timer | undefined {
			return this.world.utils.timer
		}

		init() {
			this._commondBuffer = new ECSCommandBuffer().init()
			this._commondBufferAfterUpdate = new ECSCommandBuffer().init()

			this.ctype = this["constructor"].name

			this.onInit()

			return this
		}

		protected onInit() {

		}

		onSetup?()

		instantiate(prefab: IPrefab): Entity {
			return this.world.prefabEnv.instantiate(prefab)
		}

		getCommandBuffer(): ECSCommandBuffer {
			return this._commondBuffer
		}


		getCommandBufferAfterUpdate(): ECSCommandBuffer {
			return this._commondBufferAfterUpdate
		}

		get entityManager(): EntityManager {
			return this.world.entityManager
		}

		get dataManager(): eds.DataManager {
			return this.world.dataManager
		}

		/**
		 * 
		 */
		update() {

		}

		onBeforeUpdate(): void {
			let commondBuffer = this.getCommandBuffer()
			commondBuffer.run()
			commondBuffer.clearCommonds()
		}
		onAfterUpdate(): void {
			let commondBuffer = this.getCommandBufferAfterUpdate()
			commondBuffer.run()
			commondBuffer.clearCommonds()
		}
	}

}