
namespace fsync.eds {

	export class DirtyManager {
		protected dirtyDatas: STMap<IDataClass$>

		init() {
			this.dirtyDatas = EmptyTable()
			return this
		}

		isDirty(data: IDataClass$): bool {
			return this.dirtyDatas[data.oid] != null
		}

		markDirty(data: IDataClass$) {
			this.dirtyDatas[data.oid] = data
		}

		forEachDirtyEntities(call: (data: IDataClass$) => void) {
			if (call == null) {
				return
			}

			const dirtyDatas = this.dirtyDatas
			for (let key in dirtyDatas) {
				let data = dirtyDatas[key]
				assert(data != null, "ecs data should not be null")
				call(data)
			}
		}

		clearFlags() {
			this.init()
		}
	}
}
