
namespace fsync.ecsproxy {
	export const DeleteMark = Object.create(null)

	export class EntityProxyBase {
		entity: fsync.Entity
		entityManager: fsync.EntityManager

		protected deleteOutdate() {
			for (let key of Object.keys(this)) {
				if (this[key] == DeleteMark) {
					delete this[key]
				}
			}
		}

		static getComponents<T>(proxyClz: new () => T): (new () => IComponent)[] {
			let Components = proxyClz.prototype['Components']
			// let Components = Object.getPrototypeOf(proxyClz)['Components']
			return Components
		}

		init(entityManager: fsync.EntityManager, entity: fsync.Entity) {
			this.entity = entity
			this.entityManager = entityManager

			// this.deleteOutdate()

			return this
		}

		getComponent<T extends fsync.IComponent>(cls: new () => T): T {
			return this.entityManager.getComponent(this.entity, cls)
		}

		getComponentProxy<T extends fsync.IComponent>(cls: new () => T): T & IECSComponentProxy {
			let proxy = entityProxyHelper.getComponentProxy(cls, this.entityManager, this.entity)
			return proxy
		}

		get entityId() {
			return this.entity.identity
		}

		set entityId(entityId: string) {
			this.entity = this.entityManager.getEntityById(entityId)
		}

		get isNull(): bool {
			// return this.entity == null
			return !this.entityManager.existsEntity(this.entity)
		}

		get isNotNull(): bool {
			// return this.entity == null
			return !this.isNull
		}

		isSame(t: EntityProxyBase): bool {
			return this.entityId == t.entityId
		}

		removeSelf() {
			this.entityManager.removeEntity(this.entity)
		}

		Components: (new () => fsync.IComponent)[]
		dynamicComps: { [key: string]: bool }
	}

}
