namespace fsync {

	export class ScenePrefab extends PrefabBase {
		depsEnv: ViewPrefabEnv
		get prefabMananger() {
			return PrefabManager.inst
		}

		get entityManager() {
			return this.depsEnv.entityManager
		}

		setEnv(depsEnv: ViewPrefabEnv): void {
			this.depsEnv = depsEnv
		}

		instantiate<T extends IPrefab>(prefab: T): Entity {
			return prefab.create(this.depsEnv)
		}

		instantiateEntity<T extends IPrefab>(prefab: T): Entity {
			return prefab.createEntity(this.depsEnv)
		}

		createEntity(depsEnv: ViewPrefabEnv): Entity {
			return null
		}
	}

}