namespace fsync {

	export class ECSWorld {
		name: string = "normal"
		env: WorldEnv
		entityManager: EntityManager
		dataManager: eds.DataManager
		utils: FrameSyncUtils
		prefabEnv: ViewPrefabEnv
		// 
		frameCount: number = 0

		clear() {
			this.entityManager.clearEntities()
			this.dataManager.clearDatas()
		}

		init(utils: FrameSyncUtils = null) {
			this.dataMap = EmptyTable()
			this.utils = utils || new FrameSyncUtils()

			this.env = new WorldEnv()
			this.entityManager = new EntityManager()
			this.entityManager.name = this.name
			this.entityManager.init(this.utils)

			this.dataManager = new eds.DataManager().init(this.utils)
			this.dataManager.name = this.name

			return this
		}

		mergeFrom(world: ECSWorld) {
			this.frameCount = world.frameCount
			this.utils.random.mergeFrom(world.utils.random)
			this.utils.uidTool.mergeFrom(world.utils.uidTool)
			this.utils.timer.mergeFrom(world.utils.timer)
		}

		protected dataMap: { [key: string]: any }
		getData<T extends fsync.IMerge<T>>(key: string): T {
			return this.dataMap[key]
		}

		setData<T extends fsync.IMerge<T>>(key: string, data: T) {
			this.dataMap[key] = data
		}

	}

}