namespace fsync {

	export type EntityProtoID = number
	export type EntityID = string

	export const defaultEntityID: EntityID = ""

	export class Entity {
		// protected entityPrototype: EntityProtoID
		// protected entityIndex: number
		protected eid: EntityID

		get identity(): EntityID {
			return this.eid
		}

		clone(): Entity {
			return this
		}

		static fromId(id: EntityID) {
			let entity = new Entity()
			entity.eid = id
			return entity
		}
	}

}
