namespace fsync {

	const sortCmds = (cmds: IGameInputCmd[]) => {
		cmds.sort((a, b) => a.cmdIndex - b.cmdIndex)
	}

	/**
	 * 
	 */
	export class SinglePortCmdBuffer implements IMerge<SinglePortCmdBuffer> {
		name: string = "unknown"

		roleId: TRoleId

		protected cmds: IGameInputCmd[]

		protected cmdReorderQueue: CmdReorderQueue

		protected curFrameCount: number
		protected curOutdateCmdIndex: number

		/**
		 * 
		 */
		curCmdIndex: number

		init(roleId: TRoleId) {
			this.roleId = roleId
			this.cmds = []
			this.cmdReorderQueue = new CmdReorderQueue().init()
			this.curCmdIndex = 0
			this.curFrameCount = 0
			this.curOutdateCmdIndex = 0

			return this
		}

		protected latestNetCmd: IGameInputCmd = null
		getLatestNetCmd() {
			return this.latestNetCmd
		}

		latestOrderedCmdIndex: number = 0
		/**
		 * cmdIndex,net cmd
		 */
		getOrderedNetCmd() {
			sortCmds(this.cmds)
			let cmdIndex = this.latestOrderedCmdIndex
			this.cmds.forEach(cmd => {
				if (cmd.cmdIndex == cmdIndex + 1) {
					cmdIndex += 1
				}
			})

			this.latestOrderedCmdIndex = cmdIndex

			return this.cmds.find(cmd => cmd.cmdIndex == cmdIndex)
		}

		protected latestLocalCmd: IGameInputCmd = null
		/**
		 * cmdIndex, 
		 */
		getLatestLocalCmd(): IGameInputCmd {
			return this.latestLocalCmd
		}

		putCmd(cmd: IGameInputCmd) {
			if (cmd.route == "local") {
				this._putCmd(cmd)
			} else {
				this.cmdReorderQueue.put(cmd)
				this.flushNetCmds()
				this.adjustSurgedCmds(cmd)
			}
		}

		// 
		protected flushNetCmds() {
			const cmdReorderQueue = this.cmdReorderQueue
			while (true) {
				let cmd = cmdReorderQueue.pop()
				if (cmd == null) {
					break
				}
				this._putCmd(cmd)
			}

		}

		needSync: boolean = false

		protected _putCmd(cmd: IGameInputCmd) {
			if (cmd.route == "net") {
				if (this.latestNetCmd == null || this.latestNetCmd.cmdIndex < cmd.cmdIndex) {
					this.latestNetCmd = cmd
				}
			}

			let c = this.cmds.find(c => c.cmdId == cmd.cmdId)
			if (c != null) {
				if (c.route == "net") {
					//pass
				} else if (c.route == "local") {
					// netlocal
					if (cmd.route == "net") {
						this.needSync = true
						// console.log("replace", c.cmdIndex, '->', cmd.cmdIndex)
						let index = this.cmds.indexOf(c)
						this.cmds[index] = cmd
					}
				}
			} else {
				if (cmd.route == "net") {
					this.needSync = true
				}
				// netlocal
				this.cmds.push(cmd)
			}
		}

		// TODO: 
		surLastCmd: IGameInputCmd
		adjustSurgedCmds(triggerCmd: IGameInputCmd) {
			let surLastCmd = this.surLastCmd

			//#region 
			// , 
			if (surLastCmd != null
				&& triggerCmd != null
				&& (surLastCmd.cmdIndex + 1 != triggerCmd.cmdIndex)
			) {
				return
			}
			//#endregion

			let cmds = this.cmds.filter(cmd => cmd.route == "net")
			if (cmds.length > 0) {
				if (surLastCmd == null) {
					surLastCmd = cmds[0]
					cmds = cmds.slice(1)
				}
				cmds.forEach((cmd, index) => {
					if (surLastCmd.cmdIndex + 1 == cmd.cmdIndex) {
						let localDiff = cmd.createFrameCount - surLastCmd.createFrameCount
						let netDiff = cmd.frameCount - surLastCmd.frameCount
						let diff = netDiff - localDiff
						if (0 < diff
							// && diff < 5
						) {
							if (!cmd.isAdjustedForSurge) {
								cmd.isAdjustedForSurge = true

								cmd.netFrameCount = cmd.frameCount

								// let predictFrameCount = surLastCmd.frameCount + localDiff
								let maxDiff = 3
								if (diff > 5) {
									maxDiff = 2
								}
								let predictFrameCount = cmd.frameCount - Math.min(diff, maxDiff)
								if (false) {
									console.log(`adjustSurgedCmds:(${surLastCmd.cmdIndex},${surLastCmd.createFrameCount},${surLastCmd.frameCount})`
										+ `,(${cmd.cmdIndex},${cmd.createFrameCount},${cmd.frameCount})`
										+ `|${cmd.frameCount} -> ${predictFrameCount}`)
								}
								cmd.frameCount = predictFrameCount
								if (cmd.frameCount < 0) {
									console.error("lkjwefklj")
								}
							}
						}
						surLastCmd = cmd
					}
				})
			}
			this.surLastCmd = surLastCmd
		}

		popFrameCmds(frameCount: number, pops: IGameInputCmd[]): IGameInputCmd[] {
			sortCmds(this.cmds)

			// const outdateFrameCount = this.curFrameCount
			const outdateCmdIndex = this.curCmdIndex
			let curCmdIndex = this.curCmdIndex
			for (let cmd of this.cmds) {
				if (curCmdIndex + 1 == cmd.cmdIndex && cmd.frameCount <= frameCount) {
					this.processFrameCmdsSurge(cmd)
					pops.push(cmd)

					if (curCmdIndex != cmd.cmdIndex - 1) {
						console.warn("invalid cmdIndex")
					}
					curCmdIndex = Math.max(curCmdIndex, cmd.cmdIndex)
				}
			}

			this.curCmdIndex = curCmdIndex
			this.curFrameCount = frameCount

			this.cleanOutdateCmds()

			return pops
		}

		/**
		 * 
		 * - 
		 * - pops  cmdIndex  (cmdIndex0 < cmdIndex1 && frameCount0 >= frameCount1) cmdIndex1  cmd 
		 * - 
		 */
		curNetFrameCount: number = -1
		/**
		 * 
		 */
		surgeTimes: number = 0

		/**
		 * 
		 */
		allowSurgeTimesMax: number = 1
		processFrameCmdsSurge(cmd: IGameInputCmd) {
			// sortCmds(pops)
			const allowSurgeTimesMax = this.allowSurgeTimesMax
			let frameCount = this.curNetFrameCount
			// for (let cmd of pops) 
			{
				if (cmd.route == "net") {
					if (cmd.frameCount <= frameCount) {
						let isSurge = true
						if (cmd.frameCount > frameCount - 2) {
							if (this.surgeTimes <= allowSurgeTimesMax) {
								this.surgeTimes++
								isSurge = false
							}
						}
						if (isSurge) {
							cmd.isSurge = true
							console.warn("marksurge:", this.name, this.roleId, cmd.cmdIndex, "for", frameCount, ">=", cmd.frameCount)
						}
					} else {
						// 
						frameCount = cmd.frameCount
						this.surgeTimes = 0
					}
					this.curNetFrameCount = frameCount
				}
			}
		}

		/**
		 * 
		 */
		protected cleanOutdateCmds() {
			let cmds = this.cmds
			sortCmds(cmds)

			let outdateIndex = this.curOutdateCmdIndex
			let delIndex = 0
			for (let cmd of cmds) {
				if (cmd.cmdIndex == outdateIndex + 1 && cmd.frameCount <= this.curFrameCount && cmd.route == "net") {
					// delete
					delIndex++
					outdateIndex++
				}
			}
			this.curOutdateCmdIndex = outdateIndex
			// this.cmds = cmds.slice(delIndex)
			this.cmds = cmds.filter(c => c.cmdIndex >= outdateIndex - 8)
		}

		// /**
		//  * 
		//  */
		// cleanOutdateLocalCmdsForce() {
		// 	let cmds = this.cmds
		// 	sortCmds(cmds)

		// 	let outdateIndex = this.curOutdateCmdIndex + 1
		// 	let delIndex = 0
		// 	for (let cmd of cmds) {
		// 		if (cmd.cmdIndex <= outdateIndex && cmd.frameCount <= this.curFrameCount) {
		// 			// delete
		// 			delIndex++
		// 			outdateIndex++
		// 		}
		// 	}
		// 	this.curOutdateCmdIndex = outdateIndex - 1
		// 	this.cmds = cmds.slice(delIndex - 12)
		// }

		mergeFrom(cmdBuffer: SinglePortCmdBuffer) {
			cmdBuffer.flushNetCmds()
			this.flushNetCmds()

			let tCmds = this.cmds
			let sCmds = cmdBuffer.cmds

			let cmdIdMap = EmptyTable()
			for (let cmd of tCmds) {
				cmdIdMap[cmd.cmdId] = true
			}

			for (let i = sCmds.length - 1; i > -1; i--) {
				let cmd = sCmds[i]
				if (!cmdIdMap[cmd.cmdId]) {
					// , bug
					tCmds.unshift(JSON.parse(JSON.stringify(cmd)))
				}
			}

			this.syncLocalCmd()

			if (cmdBuffer.surLastCmd == null) {
				this.surLastCmd = null
			} else {
				this.surLastCmd = this.cmds.find(cmd => cmd.cmdIndex == cmdBuffer.surLastCmd.cmdIndex)
			}
			this.adjustSurgedCmds(null)

			this.curCmdIndex = cmdBuffer.curCmdIndex
			this.curFrameCount = cmdBuffer.curFrameCount
			this.curOutdateCmdIndex = cmdBuffer.curOutdateCmdIndex

			this.cleanOutdateCmds()

			console.log("mergex")
		}

		syncLocalCmd() {

			// 
			let latestNetCmd: IGameInputCmd = this.getLatestNetCmd()

			if (latestNetCmd != null) {
				let diff = latestNetCmd.frameCount - latestNetCmd.createFrameCount
				if (diff < 0) {
					diff = 0
				}
				for (let cmd of this.cmds) {
					if (cmd.route == "local") {
						cmd.frameCount = cmd.createFrameCount + diff + 1
					}
				}
			}

		}

	}

	/**
	 * 
	 * - 
	 * - , /
	 */
	export class InputCmdBuffer implements IMerge<InputCmdBuffer>{
		static orderid = 0
		orderid: number = ++InputCmdBuffer.orderid
		name: string
		// constructor() {
		// 	console.error("lkwjeflk")
		// }

		route: "net" | "local" = "net"

		protected cmdBuffers: { [key: string]: SinglePortCmdBuffer } = EmptyTable()

		protected latestLocalCmd: IGameInputCmd = null
		getLatestLocalCmd() {
			return this.latestLocalCmd
		}

		protected latestUserCmd: IGameInputCmd = null
		getLatestUserCmd() {
			return this.latestUserCmd
		}

		protected latestNetCmd: IGameInputCmd = null
		getLatestNetCmd() {
			return this.latestNetCmd
		}

		protected getCmdBuffer(roleId: TRoleId): SinglePortCmdBuffer {
			let cmdBuffer = this.cmdBuffers[roleId]
			if (cmdBuffer == null) {
				cmdBuffer = new SinglePortCmdBuffer().init(roleId)
				cmdBuffer.name = this.name
				this.cmdBuffers[roleId] = cmdBuffer
			}
			return cmdBuffer
		}

		// protected netCmds: IGameInputCmd[] = []
		// /**
		//  * , 
		//  * @param cmd 
		//  */
		// putNetCmd(cmd: IGameInputCmd) {
		// 	this.netCmds.push(cmd)
		// }
		// update() {
		// 	this.netCmds.forEach(cmd => {
		// 		this.putCmd(cmd)
		// 	})
		// 	this.netCmds.length = 0
		// }

		putCmd(cmd: IGameInputCmd) {
			if (cmd.cmdType != "Pass") {
				if (this.latestUserCmd == null || this.latestUserCmd.cmdIndex < cmd.cmdIndex) {
					this.latestUserCmd = cmd
				}
			}
			if (cmd.route == "net") {
				if (this.latestNetCmd == null || this.latestNetCmd.cmdIndex == null || cmd.cmdIndex == null || this.latestNetCmd.cmdIndex < cmd.cmdIndex) {
					this.latestNetCmd = cmd
				}
			} else if (cmd.route == "local") {
				if (this.latestLocalCmd == null || this.latestLocalCmd.cmdIndex < cmd.cmdIndex) {
					this.latestLocalCmd = cmd
				}
			}

			if (cmd.cmdType == "Pass") {
				return
			}

			// if (this.name == "predict") {
			// 	// if (cmd.route == "net") {
			// 	console.log("receivecmd:", cmd.route, cmd.cmdIndex, cmd)
			// 	// }
			// }

			let cmdBuffer = this.getCmdBuffer(cmd.roleId)
			cmdBuffer.needSync = false
			cmdBuffer.putCmd(cmd)

			if (cmdBuffer.needSync) {
				cmdBuffer.needSync = false
				this.needSync = true
			}
		}

		/**
		 * 
		 */
		needSync = false
		/**
		 * 
		 */
		needSyncRightNow = false
		surgeTimes: number = 0

		popFrameCmds(frameCount: number): IGameInputCmd[] {
			let pops: IGameInputCmd[] = []
			for (let cmdBuffer of Object.values(this.cmdBuffers)) {
				cmdBuffer.popFrameCmds(frameCount, pops)
			}

			// 
			pops.sort((a, b) => {
				let n = 0
				if (a.roleId > b.roleId) {
					n += 10
				} else if (a.roleId < b.roleId) {
					n -= 10
				}
				if (a.cmdIndex > b.cmdIndex) {
					n += 1
				} else if (a.cmdIndex < b.cmdIndex) {
					n -= 1
				}
				return n
			})

			// this.needSync = false
			// for (let cmd of pops) {
			// 	if (cmd.route == "local" && cmd.needSync) {
			// 		this.needSync = true
			// 		break
			// 	}
			// }

			// if (this.name == "predict") {
			// 	pops.forEach(cmd => {
			// 		// if (cmd.route == "net") {
			// 		console.log("popcmd:", cmd.route, cmd.cmdIndex, cmd)
			// 		// }
			// 	})
			// }

			return pops
		}

		mergeFrom(cmdBuffer: InputCmdBuffer): void {
			for (let roleId in cmdBuffer.cmdBuffers) {
				let sCmdBuffer = cmdBuffer.cmdBuffers[roleId]
				let tCmdBuffer = this.cmdBuffers[roleId]
				tCmdBuffer.mergeFrom(sCmdBuffer)
			}
		}

		syncLocalCmd() {
			for (let cmdBuffer of Object.values(this.cmdBuffers)) {
				cmdBuffer.syncLocalCmd()
			}
		}

		// clearOutdateCmdsForce() {
		// 	let cmdBuffers = this.cmdBuffers
		// 	for (let roleId in cmdBuffers) {
		// 		let tCmdBuffer = cmdBuffers[roleId]
		// 		tCmdBuffer.cleanOutdateLocalCmdsForce()
		// 	}
		// }

		/**
		 * udp, cmdIndex 
		 */
		getOrderedNetCmd() {
			let cmdBuffers = ObjectUtils.values(this.cmdBuffers)
			let cmds = cmdBuffers.map(buf => buf.getOrderedNetCmd()).filter(buf => !!buf)
			let minCmd = ArrayHelper.min(cmds, cmd => cmd.frameCount)
			return minCmd
		}

	}

}