
namespace fsync.ecsproxy {
	export class EntityProxyHelper {
		init() {
			this.clear()
			return this
		}

		clear() {
			this.entityProxyMap = EmptyTable()
			this.compProxyMap = EmptyTable()
		}

		/**
		* entity 
		*/
		entityProxyMap: { [key: string]: EntityProxyBase }

		getEntityGUID(t: string, entityManager: fsync.EntityManager, entity: fsync.Entity) {
			return `${t}_${entityManager.identity}_*_${entity.identity}`
		}

		getEntityProxy<T extends EntityProxyBase>(
			cls: new () => T,
			entityManager: fsync.EntityManager, entity: fsync.Entity
		): T {
			let key = this.getEntityGUID(cls.name, entityManager, entity)
			let proxy = this.entityProxyMap[key]
			if (proxy == null) {
				this.entityProxyMap[key] = proxy = new cls().init(entityManager, entity)
			}
			return proxy as T
		}

		/**
		 *  
		 */
		compProxyMap: { [key: string]: IECSComponentAttrProxyBase }

		getCompGUID(t: string, entityManager: fsync.EntityManager, entity: fsync.Entity) {
			return `${t}_${entityManager.identity}_*_${entity.identity}`
		}

		getComponentProxy<T extends fsync.IComponent>(
			cls: new () => T,
			entityManager: fsync.EntityManager, entity: fsync.Entity
		): T & IECSComponentProxy {
			let key = this.getEntityGUID(cls.name, entityManager, entity)
			let proxy = this.compProxyMap[key]
			if (proxy == null) {
				this.compProxyMap[key] = proxy = this.createComponentProxy(cls, entityManager, entity)
			}
			return proxy as any as T & IECSComponentProxy
		}

		createComponentProxy<T extends fsync.IComponent>(
			cls: new () => T,
			entityManager: fsync.EntityManager, entity: fsync.Entity
		) {
			let comp = entityManager.getComponent(entity, cls)
			let proxyProto = cls['__ecscompproxyproto']
			let proxy = Object.create(null) as IECSComponentAttrProxyBase
			Object.setPrototypeOf(proxy, proxyProto)
			proxy.__entityManager = entityManager
			proxy.setComp(comp)
			proxy.setCompCls(cls)
			proxy.__entity = entity
			return proxy
		}

		/**
		 * entityproxy -> entity
		 * @param entityManager 
		 * @param proxyClz 
		 */
		createEntityByProxy<T extends EntityProxyBase>(entityManager: EntityManager, proxyClz: new () => T): Entity {
			let Components = proxyClz.prototype['Components']
			// let Components = Object.getPrototypeOf(proxyClz)['Components']
			let entity = entityManager.createEntity(Components)
			return entity
		}

		/**
		 * entityproxy -> entity
		 * @param entityManager 
		 * @param proxy 
		 */
		createEntityProxy<T extends EntityProxyBase>(entityManager: EntityManager, proxyClz: new () => T): T {
			let Components = proxyClz.prototype['Components']
			// let Components = Object.getPrototypeOf(proxyClz)['Components']
			let entity = entityManager.createEntity(Components)
			let proxy = this.getEntityProxy(proxyClz, entityManager, entity)
			return proxy
		}

		getEntityProxyComponents<T>(proxyClz: new () => T): (new () => IComponent)[] {
			let Components = proxyClz.prototype['Components']
			// let Components = Object.getPrototypeOf(proxyClz)['Components']
			return Components
		}
	}

	export const entityProxyHelper = new EntityProxyHelper().init()
}
