	// 	toClonable: (a: any[]) => (string | number)[],
	// }

	export function ToCompConfig<T extends IConfigBase>(id: string | number): T {
		return { id } as T
	}

	/**
	 * 
	 * @param cls 
	 * @param clsid 
	 */
	export let StandCompConfig = function <T>(cls: new () => T, tables: IConfigBase[], defaultId?: string | number): T {
		if (proxyClasses.decorated) {
			return StandCompConfigSimple(cls, tables, defaultId)
		}
		return new ConfigDecoInfo(StandCompConfig, cls, tables, defaultId) as any as T
	}
	export let StandCompConfigSimple = function <T>(cls: new () => T, tables: IConfigBase[], defaultId?: string | number): T {
		return defaultId as any as T
	}

	/**
	 * 
	 */
	export class CustomDataDecoInfo extends CompDecoInfo {
		constructor(
			public t: any,
			public cls: any,
			public accessor: ICustomDataAccessor,
			public defaultId?: string | number,
		) {
			super(t, cls)
		}
	}

	/**
	 * 
	 * @param cls 
	 * @param clsid 
	 */
	export let StandCustomData = function <T>(cls: new () => T, accessor: ICustomDataAccessor, defaultId?: string | number): T {
		if (proxyClasses.decorated) {
			return StandCustomDataSimple(cls, accessor, defaultId)
		}
		return new CustomDataDecoInfo(StandCustomData, cls, accessor, defaultId) as any as T
	}
	export let StandCustomDataSimple = function <T>(cls: new () => T, accessor: ICustomDataAccessor, defaultId?: string | number): T {
		return defaultId as any as T
	}

	/**
	 * 
	 */
	export class CustomDataArrayDecoInfo extends CompDecoInfo {
		constructor(
			public t: any,
			public cls: any,
			public accessor: ICustomDataAccessor,
		) {
			super(t, cls)
		}
	}

	/**
	 * 
	 * @param cls 
	 * @param clsid 
	 */
	export let StandCustomDataArray = function <T>(cls: new () => T, accessor: ICustomDataAccessor): T[] {
		if (proxyClasses.decorated) {
			return StandCustomDataArraySimple(cls, accessor)
		}
		return new CustomDataArrayDecoInfo(StandCustomDataArray, cls, accessor) as any as T[]
	}
	export let StandCustomDataArraySimple = function <T>(cls: new () => T, accessor: ICustomDataAccessor): T[] {
		return []
	}

	/**
	 * 
	 * @param cls 
	 */
	export let StandCompProxy = function <T extends fsync.IComponent>(cls: new () => T): T & IECSComponentProxy {
		if (proxyClasses.decorated) {
			return StandCompProxySimple(cls)
		}
		return new CompDecoInfo(StandCompProxy, cls) as any as T & IECSComponentProxy
	}
	export let StandCompProxySimple = function <T extends fsync.IComponent>(cls: new () => T): T & IECSComponentProxy {
		return DeleteMark as any as T & IECSComponentProxy
	}

	/**
	 * entityId 
	 * @param cls 
	 */
	export let StandEntityProxy = function <T extends EntityProxyBase>(cls: new () => T): T {
		if (proxyClasses.decorated) {
			return StandEntityProxySimple(cls)
		}
		return new CompDecoInfo(StandEntityProxy, cls) as any as T
	}
	export let StandEntityProxySimple = function <T extends EntityProxyBase>(cls: new () => T): T {
		return "" as any as T
	}

	/**
	 * entityId[] 
	 * @param cls 
	 */
	export let StandEntityProxyArray = function <T extends EntityProxyBase>(cls: new () => T): Tuple<T> {
		if (proxyClasses.decorated) {
			return StandEntityProxyArraySimple(cls)
		}
		return new CompDecoInfo(StandEntityProxyArray, cls) as any as Tuple<T>
	}
	export let StandEntityProxyArraySimple = function <T extends EntityProxyBase>(cls: new () => T): Tuple<T> {
		return ([] as string[]) as any as Tuple<T>
	}

	/**
	 * , entity,
	 * @param params 
	 */
	export function DecoDynamicComp(target: object, property: string) {
		let comps: { [key: string]: bool } = target['dynamicComps']
		if (comps == null) {
			comps = target['dynamicComps'] = {}
		}
		comps[property] = true
	}

