		static isNetworkInited: boolean = false
		/**
		 * 
		 * @param call 
		 */
		connectAsync(info: TRoomClientConnectInfo, call: (result: TRoomClientConnectResult) => void) {
			if (RoomClient.isNetworkInited) {
				const result = new TRoomClientConnectResult()
				result.indicate = RoomProtoHelper.getOkIndicate()
				result.indicate.ok = true
				this.setupRoomInstanceApi()
				call(result)
			} else {
				MGOBE.Listener.init(info.gameInfo, info.config, (evt) => {
					const result = new TRoomClientConnectResult()
					result.indicate = RoomProtoHelper.getIndicate(evt)
					if (result.indicate.ok) {
						RoomClient.isNetworkInited = true

						result.serverInfo = new TRoomServerInfo()
						result.serverInfo.serverTime = evt.data.serverTime

						this.setupRoomInstanceApi()
					}

					call(result)
				})
			}
		}

		protected setupRoomInstanceApi() {
			let room = new MGOBE.Room()
			this.matcherClient = room
			this.roomClient = room

			MGOBE.Listener.add(this.roomClient)

			this.initFrameMsgListener()
			this.initRoomClientMsgListener()

		}

		close() {
			this.intervals && this.intervals.clearAllTimer()
			if (this.roomClient) {
				MGOBE.Listener.remove(this.roomClient)
			}
		}

