		/**
		 * 
		 * @param roleInfo 
		 * @param roomInfo 
		 * @param call 
		 */
		matchRoom(roleInfo: roomserver.TRoleInfo, roomInfo: roomserver.TRoomInfo, call: (result: roomserver.TMatchJobResult) => void) {
			this.roomClient.matchRoom({
				maxPlayers: roomInfo.gameInfo.roleCount,
				roomType: `${roomInfo.roomSettings.roomType}`,
				playerInfo: {
					customPlayerStatus: 1,
					customProfile: roleInfo.basicInfo.roleId,
					name: roleInfo.basicInfo.roleName,
				}
			}, (evt) => {
				const roomInfoRaw = evt.data.roomInfo

				const result = new roomserver.TMatchJobResult()
				result.indicate = RoomProtoHelper.getIndicate(evt)
				result.opInfo = new roomserver.TRoomUserOpInfo()
				result.opInfo.serverRoleId = roleInfo.basicInfo.serverRoleId
				result.opInfo.roleId = roleInfo.basicInfo.roleId
				if (roomInfoRaw) {
					result.opInfo.roomId = roomInfoRaw.id

					if (result.indicate.ok) {
						result.roomsInfo = RoomProtoHelper.getTheOnlyRoomsInfo(roomInfoRaw)
					}
				}

				this._updateRoomInfo(evt.data.roomInfo)

				call(result)
			})
		}

		/**
		 * ID
		 * @param opInfo 
		 * @param call 
		 */
		searchRoomById(opInfo: roomserver.ITRoomUserOpInfo, call: (result: roomserver.TQueryRoomsResult) => void) {
			MGOBE.Room.getRoomByRoomId({
				roomId: `${opInfo.roomId}`,
			}, (evt) => {
				const result = new roomserver.TQueryRoomsResult()
				result.indicate = RoomProtoHelper.getIndicate(evt)
				if (result.indicate.ok) {
					result.roomsInfo = RoomProtoHelper.getTheOnlyRoomsInfo(evt.data.roomInfo)
				}

				this._updateRoomInfo(evt.data.roomInfo)

				call(result)
			})
		}

