		/**
		 * 
		 * @param call 
		 */
		listenChangedMemberNetworkState(call: (result: roomserver.TChangeMemberNetworkStateResult) => void) {
			this.roomClient.onChangePlayerNetworkState = this.roomClient.onChangePlayerNetworkState || ((evt) => {
				const info = evt.data

				const result = new roomserver.TChangeMemberNetworkStateResult()
				result.indicate = RoomProtoHelper.getOkIndicate()

				result.opInfo = new roomserver.TRoomUserOpInfo()
				result.opInfo.serverRoleId = info.changePlayerId
				result.opInfo.roleId = RoomProtoHelper.getLocalPlayerIdInRoom(info.roomInfo, info.changePlayerId)
				result.opInfo.roomId = info.roomInfo.id

				result.networkInfo = new roomserver.TRoleNetworkInfo()
				result.networkInfo.roomNetworkState = info.networkState
				result.networkInfo.relayNetworkState = info.networkState

				this._updateRoomInfo(evt.data.roomInfo)

				this.roomEvent.emit(TRoomMsgEnum.changedmembernetowrk, result)
			})
			this.roomEvent.on(TRoomMsgEnum.changedmembernetowrk, (evt) => {
				call(evt)
			})
		}

