		/**
		 * 
		 */
		onAutoRequestFrameError(call: (result: roomserver.TReqFrameRecordsResult) => void) {
			this.roomClient.onAutoRequestFrameError = this.roomClient.onAutoRequestFrameError || ((evt) => {
				const result = new roomserver.TReqFrameRecordsResult()
				result.indicate = RoomProtoHelper.getIndicate(evt.data)
				result.opInfo = new roomserver.TRoomUserOpInfo()
				result.messages = []
				if (result.indicate.ok) {
					const framesData = evt.data.data
					if (framesData) {
						framesData.frames.forEach(frame => {
							result.opInfo.roomId = frame.roomId
							frame.items.forEach((item) => {
								result.messages.push(RoomProtoHelper.getFrameInfo(frame, item))
							})
						})
					}
				}

				call(result)

				result.messages.forEach((message) => {
					this.frameEvent.emit(TRoomMsgEnum.framemsg, message)
				})
			})
			this.roomEvent.on(TRoomMsgEnum.stopframesync, (evt) => {
				call(evt)
			})
		}

		/**
		 * 
		 * @param opInfo 
		 * @param call 
		 */
		retryAutoRequestFrame(opInfo: roomserver.TRoomUserOpInfo, call: (result: roomserver.TNormalResult) => void) {
			this.roomClient.retryAutoRequestFrame()

			const result = RoomProtoHelper.getOkNormalResult(opInfo)
			call(result)
		}
