namespace fsync {

	export class Refers {
		static NormalSystem = "NormalSystem"
		static MergeSystem = "MergeSystem"
		static InputSystem = "InputSystem"
		static SyncViewSystem = "SyncViewSystem"
	}

	export class WorldMainProcess {
		/**
		 * 
		 */
		worldMain: ECSWorld
		/**
		 * 
		 */
		worldPredict: ECSWorld

		protected worlds: ECSWorld[]

		mergeSystem: EntityMergeSystem
		ecsDataMergeSystem: eds.ECSDataMergeSystem
		mainUpdater: UpdaterGroupManager
		predictUpdater: UpdaterGroupManager

		predictCmdBuffer: InputCmdBuffer
		mainCmdBuffer: InputCmdBuffer

		// 
		serverMainFrameCount: number = 0
		// 
		serverMainTime: number = 0

		clear() {
			this.mainUpdater.clear()
			this.predictUpdater.clear()
			// this.worldMain.clear()
			// this.worldPredict.clear()
		}

		init() {

			this.frameSyncStrategy = new FrameSyncStrategy().init(this)

			{
				let updateManager = new UpdaterGroupManager()
				updateManager.init()
				updateManager.setGroupUpdateOrder([
					Refers.NormalSystem,
					Refers.MergeSystem,
				])
				this.mainUpdater = updateManager
			}
			{
				let updateManager = new UpdaterGroupManager()
				updateManager.init()
				updateManager.setGroupUpdateOrder([
					Refers.NormalSystem,
					Refers.MergeSystem,
				])
				this.predictUpdater = updateManager
			}

			this.worldMain = new ECSWorld()
			this.worldMain.name = "main"
			this.worldMain.init(new FrameSyncUtils().init())

			this.worldPredict = new ECSWorld()
			this.worldPredict.name = "predict"
			this.worldPredict.init(new FrameSyncUtils().init())

			this.worlds = [this.worldMain, this.worldPredict]

			this.mergeSystem = this.createMergeSystem(this.worldMain, this.worldPredict)
			this.mainUpdater.getUpdaterGroup(Refers.MergeSystem).addUpdater(this.mergeSystem)

			this.ecsDataMergeSystem = this.createMergeSystem2(this.worldMain, this.worldPredict)
			this.mainUpdater.getUpdaterGroup(Refers.MergeSystem).addUpdater(this.ecsDataMergeSystem)
			// 
			this.mainUpdater.disableGroup(Refers.MergeSystem)

			{
				let inputCmdBuffer = new InputCmdBuffer()
				inputCmdBuffer.name = this.worldMain.name
				this.mainCmdBuffer = inputCmdBuffer
			}

			{
				let inputCmdBuffer = new InputCmdBuffer()
				inputCmdBuffer.name = this.worldPredict.name
				inputCmdBuffer.route = "local"
				this.predictCmdBuffer = inputCmdBuffer
			}

			return this
		}

		get netDelayUtil() {
			return this.frameSyncStrategy.netDelayUtil
		}

		createMergeSystem(source: ECSWorld, target: ECSWorld) {
			let mergeSystem: EntityMergeSystem = new ForceMergeSystem().init()
			mergeSystem.source = source
			mergeSystem.target = target
			return mergeSystem
		}

		createMergeSystem2(source: ECSWorld, target: ECSWorld) {
			let mergeSystem: eds.ECSDataForceMergeSystem = new eds.ECSDataForceMergeSystem().init()
			mergeSystem.source = source
			mergeSystem.target = target
			return mergeSystem
		}

		// addMergeSystem(source: ECSWorld, target: ECSWorld) {
		// 	let mergeSystem: MergeSystem = new ForceMergeSystem()
		// 	mergeSystem.init()
		// 	mergeSystem.source = source
		// 	mergeSystem.target = target
		// 	this.mainUpdater.getUpdaterGroup(Refers.MergeSystem)
		// }

		// // 
		// addBasicSystemsToWorld() {
		// 	/**
		// 	 *  -> 
		// 	 */
		// 	this.addMergeSystem(this.worldMain, this.worldPredict)
		// }

		protected frameSyncStrategy: FrameSyncStrategy

		update() {
			this.frameSyncStrategy.update()

			this.worldPredict.entityManager.cleanEntityProtoMap()
			this.worldMain.entityManager.cleanEntityProtoMap()
		}

		updatePredict() {
			this.worldPredict.frameCount++
			this.predictUpdater.onBeforeUpdate()
			this.predictUpdater.update()
			this.predictUpdater.onAfterUpdate()
		}

		syncMain(needMerge: bool = false): void {
			let latestNetCmd = this.mainCmdBuffer.getOrderedNetCmd()
			let serverMainFrameCount = latestNetCmd ? latestNetCmd.frameCount : 0
			// 
			while (this.worldMain.frameCount < serverMainFrameCount - 2) {
				this.updateMain()
			}
			if (needMerge) {
				console.log("serverMainFrameCount:", serverMainFrameCount)
				// this.mergeSystem.needMerge = true
				// this.mainUpdater.enableGroup(Refers.MergeSystem)
				/**
				 *  -1  -0  cmdIndex 
				 */
				if (this.worldMain.frameCount < serverMainFrameCount - 1) {
					this.updateMain()
					this.ecsDataMergeSystem.onBeforeUpdate()
					this.predictCmdBuffer.mergeFrom(this.mainCmdBuffer)
					//  latestNetCmd  localCmd, localCmd
					// test
					this.worldPredict.frameCount = latestNetCmd.createFrameCount
				}
				// this.mergeSystem.onBeforeUpdate()
				// this.mainUpdater.disableGroup(Refers.MergeSystem)
				// this.mergeSystem.needMerge = false
			}
		}

		syncPredictToMain() {
			// console.log("syncx")
			this.syncMain(true)
		}

		/**
		 * 
		 */
		syncPredictToCurFrame() {
			let curPredictFrameCount = this.worldPredict.frameCount
			this.syncMain(true)
			while (this.worldPredict.frameCount < curPredictFrameCount) {
				this.updatePredict()
			}
		}

		updatePredictToTheFrame(frameCount: number) {
			while (this.worldPredict.frameCount < frameCount) {
				this.updatePredict()
			}
		}

		lastTT: number = 0
		updateMain() {
			let latestCmd = this.mainCmdBuffer.getOrderedNetCmd()
			let serverMainFrameCount = latestCmd ? latestCmd.frameCount : 0
			if (this.worldMain.frameCount < serverMainFrameCount - 1) {
				// let xx = Date.now()
				// console.log("update time:", xx - this.lastTT)
				// this.lastTT = xx
				this.worldMain.frameCount++
				this.mainUpdater.onBeforeUpdate()
				this.mainUpdater.update()
				this.mainUpdater.onAfterUpdate()
			}
		}
	}

}