namespace fsync {

	export type ComponentID = string
	export type ComponentType = string

	export class ComponentHelper {
		static getIdentity(comp: IComponent): ComponentID {
			return comp["__ecs_cid"]
		}

		static getType(comp: IComponent): ComponentType {
			return comp['__ecs_ctype']
		}

		static isTypeOf(comp: IComponent, cls: new () => IComponent) {
			return comp['__ecs_ctype'] == cls.name
		}

	}

	/**
	 *  Component  IComponent
	 * Component  IClone 
	 * Component  `__ecs_cid`  `__ecs_ctype` 
	 */
	export interface IComponent {
	}

	export type TClass<T> = new () => T
	export type TComponent = new () => fsync.IComponent

}