/// <reference path="./FrameSyncUtils.ts" />

namespace fsync {

	export type BasicTypes = string | number
	export type STMap<T> = { [key: string]: T }

	/**
	 * Entity 
	 */
	export class EntitiesContainer {
		/**
		 * entityId -> map<componentId,bool>
		 */
		entityComponentMap: { [key: string]: { [key: string]: bool } }
		/**
		 * entityId -> map<componentType,componentId>
		 */
		entityProtoMap: { [key: string]: { [key: string]: ComponentID } }
		/**
		 * componentId -> entityId
		 */
		componentEntityMap: { [key: string]: EntityID }
		/**
		 * entityId -> entity
		 */
		entityMap: { [key: string]: Entity }
		/**
		 * componentId -> component
		 */
		componentMap: { [key: string]: IComponent }

		init() {
			// entityId -> map<componentId,bool>
			this.entityComponentMap = EmptyTable()
			this.entityProtoMap = EmptyTable()
			// componentId -> entityId
			this.componentEntityMap = EmptyTable()
			// entityId -> entity
			this.entityMap = EmptyTable()
			// componentId -> component
			this.componentMap = EmptyTable()
			return this
		}

		updateEntityProto(entity: Entity) {
			let proto = EmptyTable()
			this.entityProtoMap[entity.identity] = proto

			let comps = this.entityComponentMap[entity.identity]
			if (comps != null) {
				for (let key of Object.keys(comps)) {
					let comp = this.componentMap[key]
					let t = ComponentHelper.getType(comp)
					proto[t] = ComponentHelper.getIdentity(comp) || true
				}
			}
		}

		cleanEntityProtoMap() {
			let newMap: { [key: string]: { [key: string]: string } } = EmptyTable()
			const entityProtoMap = this.entityProtoMap
			for (let key in entityProtoMap) {
				if (this.entityMap[key]) {
					newMap[key] = entityProtoMap[key]
				}
			}
			this.entityProtoMap = newMap
		}

		addComponent(entity: Entity, comp: IComponent) {
			const compId = ComponentHelper.getIdentity(comp)
			if (this.componentMap[compId] != null) {
				assert(false, "comp duplicate add")
			}
			if (this.entityMap[entity.identity] == null) {
				assert(false, "entity not exist")
			}
			this.entityComponentMap[entity.identity][compId] = true
			this.componentEntityMap[compId] = entity.identity
			this.componentMap[compId] = comp

			this.updateEntityProto(entity)
		}

		removeComponentByType(entity: Entity, t: ComponentType): IComponent {
			let compMap = this.entityComponentMap[entity.identity]
			for (let key in compMap) {
				if (ComponentHelper.getType(this.componentMap[key]) == t) {
					let comp = this.componentMap[key]
					delete this.componentEntityMap[key]
					delete this.componentMap[key]
					delete compMap[key]

					this.updateEntityProto(entity)
					return comp
				}
			}
			return null
		}
		removeComponentsByType(entity: Entity, t: ComponentType) {
			let compMap = this.entityComponentMap[entity.identity]
			let removeList = []
			for (let key in compMap) {
				if (ComponentHelper.getType(this.componentMap[key]) == t) {
					removeList.push(key)
				}
			}
			for (let key of removeList) {
				delete this.componentEntityMap[key]
				delete this.componentMap[key]
			}

			this.updateEntityProto(entity)
		}
		protected _removeComponent(entity: Entity, comp: IComponent) {
			const compId = ComponentHelper.getIdentity(comp)
			delete this.componentEntityMap[compId]
			delete this.componentMap[compId]
			delete this.entityComponentMap[entity.identity][compId]
		}
		removeComponent(entity: Entity, comp: IComponent) {
			this._removeComponent(entity, comp)
			this.updateEntityProto(entity)
		}
		removeComponents(entity: Entity, comps: IComponent[]) {
			for (let comp of comps) {
				this._removeComponent(entity, comp)
			}
			this.updateEntityProto(entity)
		}

		removeAllComponents(entity: Entity) {
			let compMap = this.entityComponentMap[entity.identity]
			for (let key in compMap) {
				delete this.componentEntityMap[key]
				delete this.componentMap[key]
			}
			this.entityComponentMap[entity.identity] = EmptyTable()

			this.updateEntityProto(entity)
		}

		addEntity(entity: Entity) {
			if (this.entityMap[entity.identity] != null) {
				assert(false, "entity duplicate add")
			}
			this.entityMap[entity.identity] = entity
			if (this.entityComponentMap[entity.identity] == null) {
				this.entityComponentMap[entity.identity] = EmptyTable()
			}

			this.updateEntityProto(entity)
		}

		getEntityById(entityId: EntityID): Entity {
			return this.entityMap[entityId]
		}

		removeEntity(entity: Entity) {
			if (this.entityComponentMap[entity.identity] != null) {
				let comps = this.entityComponentMap[entity.identity]
				for (let compId in comps) {
					delete this.componentEntityMap[compId]
					delete this.componentMap[compId]
				}
				delete this.entityComponentMap[entity.identity]
			}
			delete this.entityMap[entity.identity]

			this.updateEntityProto(entity)
		}

		getComponentById(entity: Entity, compId: string): IComponent {
			let compMap = this.entityComponentMap[entity.identity]
			if (compMap[compId]) {
				return this.componentMap[compId]
			} else {
				return undefined
			}
		}

		getComponent(entity: Entity, componentType: ComponentType): IComponent {
			// let components = this.entityComponentMap[entity.identity]
			// let componentMap = this.componentMap
			// for (let key in components) {
			// 	if (ComponentHelper.getType(componentMap[key]) == componentType) {
			// 		return componentMap[key]
			// 	}
			// }
			let componentMap = this.entityProtoMap[entity.identity]
			if (componentMap == null) {
				return null
			}
			let componentId = componentMap[componentType]
			let components = this.entityComponentMap[entity.identity]
			if (components[componentId]) {
				return this.componentMap[componentId]
			}
			return null
		}

		getEntityCount() {
			return Object.keys(this.entityMap).length
		}

		getComponentOwnerEntity(comp: IComponent): Entity {
			let entityId = this.componentEntityMap[ComponentHelper.getIdentity(comp)]
			if (entityId != null) {
				return this.entityMap[entityId]
			}
			return null
		}

		getComponents(entity: Entity): IComponent[] {
			let theCompsMap = this.entityComponentMap[entity.identity]
			let compsMap = this.componentMap
			return Object.keys(theCompsMap).map(key => compsMap[key])
		}

		getComponentsWithComponent(entity: Entity, t: new () => IComponent): IComponent[] {
			let filterType = t.name
			let theCompsMap = this.entityComponentMap[entity.identity]
			let compsMap = this.componentMap
			return Object.keys(theCompsMap).map(key => compsMap[key]).filter(comp => ComponentHelper.getType(comp) == filterType)
		}

		forEachEntities(componentTypes: ComponentType[], withoutComponentTypes: ComponentType[], call: (entity: Entity) => void | bool) {
			if (call == null) {
				return
			}

			for (let key in this.entityMap) {
				let comps = this.entityProtoMap[key]
				// let findKeys = '_' + Object.keys(comps).join('_') + '_'
				// true
				let matched = true
				for (let ct of componentTypes) {
					if (!comps[ct]) {
						// if (findKeys.indexOf('_' + ct + '_') < 0) {
						matched = false
						break
					}
				}
				if (matched) {
					for (let ct of withoutComponentTypes) {
						if (comps[ct]) {
							// if (findKeys.indexOf('_' + ct + '_') < 0) {
							matched = false
							break
						}
					}
				}
				if (matched) {
					if (call(this.entityMap[key]) == true) {
						break
					}
				}
			}

		}

		existsEntity(entity: Entity): bool {
			return this.entityMap[entity.identity] != null
		}

		clearEntities() {
			this.init()
		}

	}

	export class EntityDirtyManager {
		protected dirtyComps: IComponent[]
		protected dirtyEntities: STMap<Entity>

		init() {
			this.dirtyComps = []
			this.dirtyEntities = EmptyTable()
			return this
		}

		isEntityDirty(entity: Entity): bool {
			return this.dirtyEntities[entity.identity] != null
		}

		isComponentDirty(comp: IComponent): bool {
			return this.dirtyComps.indexOf(comp) >= 0
		}

		markComponentDirty(comp: IComponent) {
			this.dirtyComps.push(comp)
		}

		markEntityDirty(entity: Entity) {
			this.dirtyEntities[entity.identity] = entity
		}

		sortDirtyEntities(entityManager: EntityManager) {
			for (let comp of this.dirtyComps) {
				// let entity = comp["__entity"]
				let entity = entityManager.getComponentOwnerEntity(comp)
				if (entity != null) {
					this.dirtyEntities[entity.identity] = entity
				}
			}
		}

		forEachDirtyEntities(call: (entity: Entity) => void) {
			if (call == null) {
				return
			}

			for (let key in this.dirtyEntities) {
				let entity = this.dirtyEntities[key]
				assert(entity != null, "entity should not be null")
				call(entity)
			}
		}

		clearFlags() {
			this.dirtyComps = []
			this.dirtyEntities = EmptyTable()
		}
	}


	let _EntityManagerIdAcc = 0
	export class EntityManager {
		name: string
		dirtyManager: EntityDirtyManager
		entityContainer: EntitiesContainer
		utils: FrameSyncUtils
		identity: string

		constructor() {
			this.identity = `EntityManager_${_EntityManagerIdAcc++}`
		}

		init(utils: FrameSyncUtils) {
			this.dirtyManager = new EntityDirtyManager().init()
			this.entityContainer = new EntitiesContainer().init()
			this.utils = utils
		}

		clearDirtyManager() {
			this.dirtyManager.clearFlags()
		}

		createQuery(): EntityQuery {
			return new EntityQuery().init(this)
		}

		createEntity(tcomps: (new () => IComponent)[]): Entity {
			let entityIdPrefix = tcomps.map(comp => comp.name).sort().join('_')
			const entity = new Entity()
			entity["eid"] = this.utils.uidTool.genTypedId(entityIdPrefix)
			this.entityContainer.addEntity(entity)

			this.addComponents(entity, tcomps)

			return entity
		}

		protected decoComponent(entity: Entity, comp: IComponent) {
			DecoECSComponent(comp, this.dirtyManager, this.utils.uidTool)
			// comp['__entity'] = entity
		}

		getEntityById(entityId: EntityID): Entity {
			return this.entityContainer.getEntityById(entityId)
		}

		wrapEntityId(entityId: EntityID): Entity {
			let entity = this.entityContainer.getEntityById(entityId)
			if (entity == null) {
				entity = Entity.fromId(entityId)
			}
			return entity
		}

		addComponent<T extends IComponent = IComponent>(entity: Entity, tcomp: new () => T): T {
			return this.attachComponent(entity, new tcomp())
		}

		attachComponent<T extends IComponent = IComponent>(entity: Entity, comp: T): T {
			this.decoComponent(entity, comp)
			this.entityContainer.addComponent(entity, comp)
			this.dirtyManager.markEntityDirty(entity)
			return comp
		}

		addComponents(entity: Entity, tcomps: (new () => IComponent)[]): IComponent[] {
			let comps = tcomps.map(tcomp => new tcomp())
			this.attachComponents(entity, comps)
			return comps
		}

		attachComponents<T extends IComponent = IComponent>(entity: Entity, comps: T[]): T[] {
			for (let comp of comps) {
				this.decoComponent(entity, comp)
				this.entityContainer.addComponent(entity, comp)
			}
			this.dirtyManager.markEntityDirty(entity)
			return comps
		}

		dettachComponent<T extends IComponent = IComponent>(entity: Entity, comp: T): T {
			this.entityContainer.removeComponent(entity, comp)
			this.dirtyManager.markEntityDirty(entity)
			return comp
		}

		dettachComponents<T extends IComponent = IComponent>(entity: Entity, comps: T[]): T[] {
			this.entityContainer.removeComponents(entity, comps)
			this.dirtyManager.markEntityDirty(entity)
			return comps
		}

		removeComponent<T extends IComponent = IComponent>(entity: Entity, t: new () => T): T {
			const ret = this.entityContainer.removeComponentByType(entity, t.name) as T
			this.dirtyManager.markEntityDirty(entity)
			return ret
		}

		removeComponents<T extends IComponent = IComponent>(entity: Entity, t: new () => T): void {
			const ret = this.entityContainer.removeComponentsByType(entity, t.name)
			this.dirtyManager.markEntityDirty(entity)
			return ret
		}

		removeEntity(entity: Entity) {
			this.entityContainer.removeEntity(entity)
			this.dirtyManager.markEntityDirty(entity)
		}

		/**
		 * entityidentity
		 */
		isValidEntity(entity: Entity): bool {
			return entity != null && entity.identity != null
		}

		/**
		 * entitymananger
		 * @param entity 
		 */
		existsEntity(entity: Entity): bool {
			if (entity != null) {
				return this.entityContainer.existsEntity(entity)
			} else {
				return false
			}
		}

		/**
		 * 
		 * @param entity1 
		 * @param entity2 
		 */
		isSameEntity(entity1: Entity, entity2: Entity): bool {
			return entity1.identity == entity2.identity
		}

		/**
		 * 
		 * @param entity1 
		 * @param entity2 
		 */
		isSameEntitySafe(entity1: Entity, entity2: Entity): bool {
			if (entity1 && entity2) {
				return entity1.identity == entity2.identity
			} else {
				return false
			}
		}

		addEntity(entity: Entity) {
			this.entityContainer.addEntity(entity)
			this.dirtyManager.markEntityDirty(entity)
		}

		getComponents<T extends IComponent = IComponent>(entity: Entity, t: new () => IComponent = null): T[] {
			if (t == null) {
				return this.entityContainer.getComponents(entity) as T[]
			} else {
				return this.entityContainer.getComponentsWithComponent(entity, t) as T[]
			}
		}

		getComponentById<T extends IComponent = IComponent>(entity: Entity, compId: string): T {
			return this.entityContainer.getComponentById(entity, compId) as T
		}

		getComponentByType<T extends IComponent = IComponent>(entity: Entity, componentType: string): T {
			return this.entityContainer.getComponent(entity, componentType) as T
		}

		getComponent<T extends IComponent = IComponent>(entity: Entity, t: new () => T): T {
			return this.entityContainer.getComponent(entity, t.name) as T
		}

		getOrAddComponent<T extends IComponent = IComponent>(entity: Entity, t: new () => T): T {
			let comp = this.getComponent(entity, t)
			if (comp == null) {
				comp = this.addComponent(entity, t)
			}
			return comp
		}

		existComponent<T extends IComponent = IComponent>(entity: Entity, t: new () => T): bool {
			return this.entityContainer.getComponent(entity, t.name) != undefined
		}

		getEntityCount() {
			return this.entityContainer.getEntityCount()
		}

		getComponentOwnerEntity(comp: IComponent): Entity {
			return this.entityContainer.getComponentOwnerEntity(comp)
		}

		/**
		 * entity
		 * @param entity 
		 * @param targetManager 
		 */
		cloneEntity(entity: Entity, targetManager: EntityManager): Entity {
			let copyComps: IComponent[] = []
			let comps = targetManager.getComponents(entity)
			assert(comps != null, TestHelper.UNMATCHED_RESULT)
			for (let comp of comps) {
				copyComps.push(CloneECSComponet(comp, this.dirtyManager))
			}
			this.addEntity(entity)
			this.attachComponents(entity, copyComps)
			return entity
		}

		/**
		 * 
		 * @param entity 
		 * @param targetManager 
		 */
		overwriteEntity(entity: Entity, targetManager: EntityManager): void {
			assert(this.entityContainer.existsEntity(entity), TestHelper.UNMATCHED_RESULT)

			this.entityContainer.removeAllComponents(entity)

			let comps = targetManager.getComponents(entity)
			assert(comps != null, TestHelper.UNMATCHED_RESULT)
			for (let comp of comps) {
				this.entityContainer.addComponent(entity, CloneECSComponet(comp, this.dirtyManager))
			}
		}

		cleanEntityProtoMap() {
			this.entityContainer.cleanEntityProtoMap()
		}

		/**
		 * entity
		 */
		clearEntities() {
			this.dirtyManager.clearFlags()
			this.entityContainer.clearEntities()
		}

		/**
		 * 
		 * @param entity 
		 * @param tcomp 
		 * @param compData 
		 */
		setComponentData<T extends fsync.IComponent>(entity: fsync.Entity, tcomp: fsync.TClass<T>, compData: T): T {
			let comp = this.getOrAddComponent(entity, tcomp)
			for (let key in compData) {
				comp[key] = compData[key]
			}
			return comp
		}

	}

}