namespace fsync.app {
	/**
	 * 
	 */
	export class SubWorld {
		name: string = "unkown_SubWorld"
		mainProcess: fsync.WorldMainProcess
		inputCmdBuffer: fsync.InputCmdBuffer
		timer: fsync.Timer

		world: fsync.ECSWorld
		updater: fsync.UpdaterGroupManager

		get entityManager() {
			return this.world.entityManager
		}

		get dataManager() {
			return this.world.dataManager
		}

		clear() {
			// this.world.entityManager.clearEntities()
		}

		start() {
			this.timer.setTime(Date.now())
		}

		/**
		 * 
		 * @param groupName 
		 * @param tsys 
		 */
		addSystem<T extends fsync.SystemBase>(tsys: new () => T, groupName: string): T {
			let sys = new tsys()
			sys.world = this.world
			sys.init()
			this.updater.getUpdaterGroup(groupName).addUpdater(sys)
			return sys
		}

		/**
		 * 
		 * @param sharedSlots 
		 */
		initSystems(sharedSlots: any) {

		}

		init(input: SubWorldInitInput) {
			this.world = input.world
			this.updater = input.updater
			this.mainProcess = input.mainProcess

			this.timer = this.world.utils.timer
			this.timer.setMaxDeltaTime(100)

			return this
		}

		update() {
			// this.timer.updateTime(Date.now())
		}
	}
}
