namespace fsync {

	const _copyECSComponetAttrValue = function (data: any): any {
		if (typeof (data) == "object") {
			if ((data as IClone).clone != undefined) {
				return (data as IClone).clone()
			} else {
				if (data instanceof Array) {
					// 
					return [...data]
				} else {
					throw new Error("unsupport data type to clone")
				}
			}
		} else {
			return data
		}
	}

	export const copyECSComponetAttrValue = _copyECSComponetAttrValue as <T>(data: T) => T

	export function DecoECSComponent(comp: IComponent, dirtyManager: EntityDirtyManager, uidTool: UniqueIDTool): void {
		if (comp["__ecs_cid"] != null) {
			return
		}

		// comp['__ecs_ctype'] = ""

		function buildGetSet(target: Object, property: string) {
			const value0 = target[property]
			const hiddenProp = "__" + property + "__"

			delete target[property]
			Object.defineProperty(target, property, {
				get: function () {
					return this[hiddenProp]
				},
				set: function (value) {
					let desc = Object.getOwnPropertyDescriptor(this, hiddenProp)
					if (!desc.writable) {
						throw new Error("cannot set value to a prop not writable")
					}
					this[hiddenProp] = value
					dirtyManager.markComponentDirty(this)
				}
			});

			comp[hiddenProp] = value0
		}

		for (let key in comp) {
			if (comp.hasOwnProperty(key)) {
				buildGetSet(comp, key);
			}
		}

		const typeName = comp["constructor"].name
		Object.defineProperty(comp, '__ecs_ctype', {
			value: typeName,
			writable: false,
			enumerable: false,
		})
		Object.defineProperty(comp, '__ecs_cid', {
			value: uidTool.genTypedId(typeName),
			writable: false,
			enumerable: false,
		})
		// comp['__ecs_ctype'] = typeName
		// comp["__ecs_cid"] = uidTool.genTypedId(typeName)
	}

	export function CloneECSComponet(comp: IComponent, dirtyManager: EntityDirtyManager): IComponent {
		let copy: IComponent = {
			componentType: comp['__ecs_ctype'],
		}

		// 
		for (let key in comp) {
			const data = comp[key]
			if (typeof (data) == "object") {
				if ((data as IClone).clone != undefined) {
					copy[key] = (data as IClone).clone()
				} else {
					if (data instanceof Array) {
						// 
						copy[key] = [...data]
					} else {
						throw new Error("unsupport data type to clone")
					}
				}
			} else {
				copy[key] = data
			}
		}

		// getset
		for (let key of Object.getOwnPropertyNames(comp)) {
			if (!key.startsWith("__")) {
				const hiddenProp = "__" + key + "__"
				Object.defineProperty(copy, key, {
					get: function () {
						return this[hiddenProp]
					},
					set: function (value) {
						this[hiddenProp] = value
						dirtyManager.markComponentDirty(this)
					}
				});
			}
		}

		// 
		Object.setPrototypeOf(copy, Object.getPrototypeOf(comp))

		return copy
	}

}