namespace fsync {

	export type TUserId = int64
	export type TRoleId = string
	export type TChairNo = int64
	export type TRoomId = int64
	export type TRoomSessionId = int64

	export function TRoomModel_GetRoomInfo(roomInfo: roomserver.ITRoomModel): roomserver.TRoomInfo {
		return {
			basicInfo: roomInfo.basicInfo,
			roomSettings: roomInfo.roomSettings,
			gameInfo: roomInfo.gameInfo,
		}
	}

}