
namespace fsync.app {
	export class GameSceneBase implements fsync.app.ISubScene {

		subScenes: fsync.app.SubScene[]

		sharedSlots: any
		fightWorld: fsync.app.GameWorld

		clear(): void {
			this.subScenes.forEach(scene => scene.clear())
		}
		start(): void {
			this.subScenes.forEach(scene => scene.start())
		}
		update(): void {
			this.subScenes.forEach(scene => scene.update())
		}
		init(fightWorld: fsync.app.GameWorld, sharedSlots: any) {
			this.sharedSlots = sharedSlots
			this.fightWorld = fightWorld
			this.subScenes = []

			return this
		}

		loadSubScenes(cls: new () => fsync.app.SubScene) {
			const fightWorld = this.fightWorld
			const sharedSlots = this.sharedSlots

			let mainScene = new cls().init(fightWorld.mainWorld, sharedSlots)
			this.subScenes.push(mainScene)

			let predictScene = new cls().init(fightWorld.predictWorld, sharedSlots)
			this.subScenes.push(predictScene)

			for (let scene of this.subScenes) {
				scene.initScene()
			}
		}
	}
}
