		/**
		 * 
		 * @param call 
		 */
		listenFrameSyncBroadCast(call: (message: roomserver.TReqBroadCastFrameSyncReq) => void)

		/**
		 * 
		 * @param call 
		 */
		offFrameSyncBroadCast(call: (message: roomserver.TReqBroadCastFrameSyncReq) => void)

