		/**
		 * 
		 * @param call
		 */
		listenPrepareStartGame(call: (message: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param call
		 */
		listenStartGame(call: (message: roomserver.TRespStartGameResult) => void)

		/**
		 * 
		 * @param call
		 */
		listenFetchGameOpRecords(call: (message: roomserver.TFetchGameOpRecordsResult) => void)

		/**
		 * 
		 * @param call 
		 */
