		/**
		 * 
		 * @param call
		 */
		validateRoom(opInfo: roomserver.ITRoomUserOpInfo, call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param call
		 */
		setRoomInfo(opInfo: roomserver.ITRoomUserOpInfo, roomInfo: roomserver.ITRoomSettings, call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param call
		 */
		banishMember(opInfo: roomserver.ITRoomUserOpInfo, roles: string[], call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param call
		 */
		getRoomInfo(opInfo: roomserver.ITRoomUserOpInfo, call: (result: roomserver.TGetRoomInfoResult) => void)

		/**
		 * 
		 * @param call
		 */
		fetchGameOpRecords(opInfo: roomserver.ITRoomUserOpInfo, call: (result: roomserver.TFetchGameOpRecordsResult) => void)

		/**
		 * 
		 * @param call 
		 */
		listenChangedMemberNetworkState(call: (result: roomserver.TChangeMemberNetworkStateResult) => void)

		/**
		 * 
		 * @param call 
		 */
		listenChangeCustomPlayerStatus(call: (result: roomserver.TChangeCustomPlayerStatus) => void)

		/**
		 * 
		 * @param opInfo 
		 * @param call 
		 */
		startFrameSync(opInfo: roomserver.TRoomUserOpInfo, call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param opInfo 
		 * @param call 
		 */
		stopFrameSync(opInfo: roomserver.TRoomUserOpInfo, call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 * @param call 
		 */
		onStartFrameSync(call: (result: roomserver.TNormalResult) => void)
		/**
		 * 
		 */
		onStopFrameSync(call: (result: roomserver.TNormalResult) => void)

		/**
		 * 
		 */
		onAutoRequestFrameError(call: (result: roomserver.TReqFrameRecordsResult) => void)

		/**
		 * 
		 * @param opInfo 
		 * @param call 
		 */
		retryAutoRequestFrame(opInfo: roomserver.TRoomUserOpInfo, call: (result: roomserver.TNormalResult) => void)
