namespace fsync.eds {

	/**
	 * entity
	 */
	export class ECSDataMergeSystem implements IUpdater {
		// merge from source to target
		source: ECSWorld
		target: ECSWorld

		init() {
			return this
		}

		// mg1 -> mg2
		protected mergeDiffEntities(mg1: eds.DataManager, mg2: eds.DataManager) {

			mg1.buildReferRelationMap()
			mg2.buildReferRelationMap()

			let applyMap: { [key: string]: bool } = EmptyTable()

			type EntityMap = { [key: string]: eds.IDataClass$ }
			let newMap: EntityMap = EmptyTable()
			let removeMap: EntityMap = EmptyTable()
			let modifyMap: EntityMap = EmptyTable()

			mg1.dirtyManager.forEachDirtyEntities((ecsdata) => {
				if (applyMap[ecsdata.oid]) {
					return
				}
				applyMap[ecsdata.oid] = true

				if (mg2.existsData(ecsdata)) {
					if (mg1.existsData(ecsdata)) {
						// this.onEntityUpdate(entity)
						modifyMap[ecsdata.oid] = ecsdata
					} else {
						// this.onRemoveEntity(entity)
						removeMap[ecsdata.oid] = ecsdata
					}
				} else {
					if (mg1.existsData(ecsdata)) {
						// this.onNewEntity(entity)
						newMap[ecsdata.oid] = ecsdata
					}
				}
			})

			mg2.dirtyManager.forEachDirtyEntities((ecsdata) => {
				if (applyMap[ecsdata.oid]) {
					return
				}
				applyMap[ecsdata.oid] = true

				if (mg1.existsData(ecsdata)) {
					if (mg2.existsData(ecsdata)) {
						// this.onEntityUpdate(entity)
						modifyMap[ecsdata.oid] = ecsdata
					} else {
						// this.onNewEntity(entity)
						newMap[ecsdata.oid] = ecsdata
					}
				} else {
					if (mg2.existsData(ecsdata)) {
						// this.onRemoveEntity(entity)
						removeMap[ecsdata.oid] = ecsdata
					}
				}
			})

			for (let entity of Object.values(removeMap)) {
				this.onRemoveEntity(entity)
			}

			for (let entity of Object.values(newMap)) {
				this.onNewEntity(entity)
			}

			for (let entity of Object.values(modifyMap)) {
				this.onUpdateEntity(entity)
			}

			mg1.dirtyManager.clearFlags()
			mg2.dirtyManager.clearFlags()
		}

		// entity
		onNewEntity(entity: eds.IDataClass$) {

		}

		// entity
		onRemoveEntity(entity: eds.IDataClass$) {

		}

		// entity
		onUpdateEntity(entity: eds.IDataClass$) {

		}

		onBeforeUpdate(): void {
			try {
				this.mergeDiffEntities(this.source.dataManager, this.target.dataManager)
				this.target.mergeFrom(this.source)
			} catch (e) {
				console.error(e)
			}
		}
		onAfterUpdate(): void {
		}

		update() {

		}
	}

}