const serverprotoSource = `
syntax = "proto3";
package roomserver;

// type int64 int64
// type string string
// type int64 int64
// type string string
// type string string

//id: id:int64=parseInt64(timestamp+incr(0~99999))
//type string string
//type int64 int64

// type int64 int64
// type int32 int32
// type float float
// type string string
// type string string
// type string string
// type int64 int64

message TErrorInfo {
  int32 code = 1;
  string reason = 2;
  string message = 3;
}
message TResultIndicate {
  bool ok = 1;
  TErrorInfo err = 2;
}
message TNormalResult {
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
}

message TRoomBasic {
  //
  string room_id = 1;
  //
  int64 create_time = 2;
  // uuid 
  string uuid = 3;
  //
  string conn_addr = 4;
  // 
  int64 create_type = 5;
}

message TRoomSettings {
  string room_type = 1;
  string name = 2;
  string password = 3;
  // 
  string owner_id = 4;
  // 
  bool is_forbid_join = 5;
  // ,  true  matchRoom 
  bool is_private = 6;
}

message TRoomGameInfo {
  //  /
  int32 game_mode = 1;
  //  
  int64 frame_duration = 2;
  //
  int32 role_count = 3;
  //
  float match_timeout = 4;
}

message TRoomGameState {
  int64 game_session_id = 1;
  int64 start_time = 2;
  int32 random_seed = 3;
  bool is_playing = 4;
}

message TServerInfo {
  string address = 1;
  string server_id = 2;
}

message TRoomInfo {
  TRoomBasic basic_info = 1;
  TRoomSettings room_settings = 2;
  TRoomGameInfo game_info = 3;
  TServerInfo server_info = 4;
}

message TRoomModel {
  TRoomBasic basic_info = 1;
  TRoomSettings room_settings = 2;
  TRoomGameInfo game_info = 3;
  TServerInfo server_info = 4;
  TRoomGameState game_state = 5;
  repeated TRoleModel role_models = 6;
}

message TReqGetRoomInfo { TRoomUserOpInfo op_info = 1; }

message TGetRoomInfoResult {
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  TRoomModel room_model = 3;
}

message TQueryRoomsResult {
  TResultIndicate indicate = 1;
  TRoomsInfo rooms_info = 2;
}

message TRoomsInfo {
  int32 count = 1;
  repeated TRoomModel room_models = 2;
}

message TRoomUserOpInfo {
  string room_id = 1;
  string role_id = 2;
  // id
  string role_token = 3;
}

message TMatchJobResult {
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  TRoomsInfo rooms_info = 3;
}

message TUserInfo { int64 user_id = 1; }

message TRoleBasic {
  // id
  string role_id = 1;
  // 
  int32 sex = 2;
  // 
  string role_name = 3;
  // uri
  string role_head_uri = 4;
  // roleId
  string server_role_id = 5;
  // 
  bool is_robot = 6;
}

message TRoleGameInfo {
  //
  int32 score = 1;
  //
  int32 level = 2;
  //
  int32 battle_count = 3;
  //
  float win_rate = 4;
}

message TRoleRoomState {
  string room_id = 1;
  int64 chair_no = 2;
  //
  bool is_conn_active = 3;
  bool is_master = 4;
}

enum TRolePlayState {
  PENDING = 0;
  READY = 1;
  PLAYING = 2;
}

message TRoleGameState { TRolePlayState state = 1; }

message TRoleInfo {
  TRoleBasic basic_info = 1;
  TUserInfo user_info = 2;
  TRoleGameInfo game_info = 3;
  TRoleRoomState room_state = 4;
}

message TRoleModel {
  TRoleBasic basic_info = 1;
  TUserInfo user_info = 2;
  TRoleGameInfo game_info = 3;
  TRoleRoomState room_state = 4;
  TRoleGameState game_state = 5;
  TRoleNetworkInfo netwok_info = 6;
}

message TRoomMemberFilterInfo {}

message THandleResult { TResultIndicate indicate = 1; }

message TRoomPlayerMessageOptions {}
message TRoomPlayerMessage { TRoomPlayerMessageOptions options = 1; }

message TReqEnterRoom {
  TRoomUserOpInfo op_info = 1;
  TRoleInfo role_info = 2;
  TRoomModel room_info = 3;
}

message TReqExitRoom { TRoomUserOpInfo op_info = 1; }

message TReqDestroyRoomForce { TRoomUserOpInfo op_info = 1; }

message TReqRoleBroadOptions { string role_id = 1; }
message TFrameSyncInfo {
  //  
  int64 server_time = 1;
  //  
  int64 server_frame_count = 2;
  //
  int64 client_time = 3;
  // , 
  int64 client_lerp_time = 4;
  // 
  int32 random_seed = 5;
  // 
  bool is_replay = 6;
}

message TReqBroadCastFrameSyncReq {
  //  
  TRoomUserOpInfo op_info = 1;
  TFrameSyncInfo sync_info = 2;
  bytes msg_bytes = 4;
}

message TReqBroadCastClientMessage {
  //  
  TRoomUserOpInfo op_info = 1;
  TFrameSyncInfo sync_info = 2;
  //  
  repeated TReqRoleBroadOptions targets = 3;
  bytes msg_bytes = 4;
  string msg_str = 5;
}

message TReqValidateRoom { TRoomUserOpInfo op_info = 1; }

message TReqSetRoomInfo {
  TRoomUserOpInfo op_info = 1;
  TRoomSettings room_info = 2;
}

message TReqBanishMember {
  TRoomUserOpInfo op_info = 1;
  repeated string roles = 2;
}

message TReqSetSelfRoomChairNo {
  TRoomUserOpInfo op_info = 1;
  int64 chair_no = 2;
}

message TReqFilterMembers {
  TRoomUserOpInfo op_info = 1;
  TRoomMemberFilterInfo filter_info = 2;
}

message TStartGameOptions {}

message TReqStartGame {
  TRoomUserOpInfo op_info = 1;
  TStartGameOptions start_options = 2;
}

message TFrameSyncInitConfig { int32 random_seed = 1; }
message TRespStartGameResult {
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  TFrameSyncInitConfig frame_sync_init_config = 3;
}

message TReqSearchRoomById { TRoomUserOpInfo op_info = 1; }

message TReqGetRecommendRooms { TRoomUserOpInfo op_info = 1; }

message TReqMatchUsersWithDefaultRule {
  TRoomUserOpInfo op_info = 1;
  TRoleInfo role_info = 2;
  TRoomInfo room_info = 3;
}

message TReqNotifyCreateRoom { TRoomModel room_model = 1; }

message TReqNotifyRemoveRoom { TRoomUserOpInfo op_info = 1; }

message TReqFetchGameOpRecords { TRoomUserOpInfo op_info = 1; }

message TFetchGameOpRecordsResult {
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  repeated TReqBroadCastFrameSyncReq sync_op_records = 3;
}

message TReqHeartBeat { TRoomUserOpInfo op_info = 1; }

message THeartBeatResult { TRoomUserOpInfo op_info = 1; }

message TRoomServerRegisterForMatcherServerInfo {
  //ID
  string server_id = 1;
  //ID
  string conn_id = 2;
  //
  string client_conn_addr = 3;
  //
  int64 room_count = 4;
}

message TRoomServerRegisterForMatcherServerResult {
  TResultIndicate indicate = 1;
}

message TRoomServerUnregisterForMatcherServerInfo {
  //ID
  string server_id = 1;
  //ID
  string conn_id = 2;
  //
  string client_conn_addr = 3;
}

message TRoomServerUnregisterForMatcherServerResult {
  TResultIndicate indicate = 1;
}

// string 64MB
message TReqDownloadProto {
  //  proto
  int32 proto_version = 1;
  // 
  bool force = 2;
}

message TProtoInfo {
  //  proto
  int32 version = 1;
  //   proto_content
  string content = 2;
}

message TDownloadProtoResult {
  TResultIndicate indicate = 1;
  TProtoInfo proto_info = 2;
}

message TRoleNetworkInfo{
  int32 room_network_state = 1;
  int32 relay_network_state = 2;
}

message TChangeMemberNetworkStateResult{
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  TRoleNetworkInfo network_info = 3;
}

message TChangeCustomPlayerStatus{

}

message TReqFrameRecordsInfo{
  int32 begin_frame = 1;
  int32 end_frame = 2;
}

message TReqFrameRecordsResult{
  TResultIndicate indicate = 1;
  TRoomUserOpInfo op_info = 2;
  repeated TReqBroadCastFrameSyncReq messages = 3;
}

// 
message TActorMoveInfo{
  int32 x = 1;
  int32 y = 2;
  string actor_id = 3;
}

// RPG
message TRPGPlayerCmd {
  // id
  int32 role_id = 1;
  // Id
  int32 cmd_id = 2;
  // 
  int64 create_time = 3;
  // 
  int32 create_frame_count = 4;
  // 
  int32 frame_count = 5;
  // 
  int32 cmd_index = 6;
  // // ,false ,true 
  // bool cmd_type = 7;
  // // 
  // bool need_sync = 8;
  // // 
  // bool use_skill = 9;
  // 
  int64 cmd_flags = 7;
  // 
  TActorMoveInfo move = 8;
}

`
const fileBaseName = 'serverproto'
const srcFile = `./src/protos/${ fileBaseName }.proto`
fsync.protoPool.put(srcFile, serverprotoSource)
