
namespace fsync {

	export class ViewBindManager {
		protected entityViewMap: { [key: string]: IView } = EmptyTable()

		init() {
			this.entityViewMap = EmptyTable()
			return this
		}

		getEntityView(entity: Entity): IView {
			return this.entityViewMap[entity.identity]
		}

		bindEntityView(entity: Entity, view: IView) {
			this.entityViewMap[entity.identity] = view
		}

		unbindEntityView(entity: Entity) {
			delete this.entityViewMap[entity.identity]
		}

		removeEntity(entity: Entity) {
			this.removeEntityById(entity.identity)
		}

		removeEntityById(entityId: EntityID) {
			let view = this.entityViewMap[entityId]
			view.destroy()
			delete this.entityViewMap[entityId]
		}

		getAllEntityID(): EntityID[] {
			return Object.keys(this.entityViewMap)
		}

		clear() {
			for (let key of this.getAllEntityID()) {
				this.removeEntityById(key)
			}
		}
	}

}