
namespace fsync.ecsproxy {
	/**
	 * comp 
	 * @param target
	 */
	export function DecoCompProxy<T extends fsync.IComponent>(target: new () => T) {
		proxyClasses.compProxies.push(target)
	}

	export function DoDecoCompProxy<T extends fsync.IComponent>(target: new () => T) {
		if (target['__ecscompproxyproto']) {
			return
		}

		let demo = new target()
		// 
		let proto = Object.create(null)
		let protoRaw = target.prototype
		for (let key of Object.keys(demo)) {
			if (demo[key] instanceof CompDecoInfo) {
				const decoInfo = demo[key] as CompDecoInfo
				const cls = decoInfo.cls
				const clst = decoInfo.t
				protoRaw[key] = ""
				if (clst == StandEntityProxy) {
					//  component  entity
					Object.defineProperty(proto, key, {
						get: function () {
							const self = (this as IECSComponentAttrProxyBase)
							let entityManager = self.__entityManager as fsync.EntityManager
							let entityId = self.__comp[key]
							let entity = entityManager.wrapEntityId(entityId)
							let proxy = entityProxyHelper.getEntityProxy(cls, entityManager, entity)
							return proxy
						},
						set: function (value: EntityProxyBase) {
							const self = (this as IECSComponentAttrProxyBase)
							self.__comp[key] = value.entityId
						},
					})
				} else if (clst == StandEntityProxyArray) {
					Object.defineProperty(proto, key, {
						get: function () {
							const self = (this as IECSComponentAttrProxyBase)
							let entityManager = self.__entityManager as fsync.EntityManager
							let entityIds = self.__comp[key] as string[]
							let proxys = entityIds.map(entityId => {
								let entity = entityManager.wrapEntityId(entityId)
								return entityProxyHelper.getEntityProxy(cls, entityManager, entity)
							})
							return proxys
						},
						set: function (values: EntityProxyBase[]) {
							const self = (this as IECSComponentAttrArrayProxyBase)
							let entityIds = self.__comp[key] as string[]
							entityIds.length = 0
							values.forEach(value => {
								entityIds.push(value.entityId)
							})
						},
					})
				} else if (clst == StandCompConfig && decoInfo instanceof ConfigDecoInfo) {
					const defaultId = decoInfo.defaultId
					const tables = decoInfo.tables
					Object.defineProperty(proto, key, {
						/**
						 *  get  config
						 */
						get: function () {
							const self = (this as IECSComponentAttrProxyBase)
							let id = self.__comp[key] as string
							let config = tables.find(table => table.id == id)
							return config
						},
						/**
						 *  set  IConfigBase
						 * @param id
						 */
						set: function (config: IConfigBase) {
							const self = (this as IECSComponentAttrProxyBase)
							self.__comp[key] = config.id
						},
					})
					// id
					protoRaw[key] = defaultId
				} else if (clst == StandCustomData && decoInfo instanceof CustomDataDecoInfo) {
					const defaultId = decoInfo.defaultId
					const accessor = decoInfo.accessor
					let { toClonable, toTarget, } = accessor
					Object.defineProperty(proto, key, {
						/**
						 *  get  data id
						 */
						get: function () {
							const self = (this as IECSComponentAttrProxyBase)
							let id = self.__comp[key] as string
							let data = toTarget(id, self.__comp, key)
							return data
						},
						/**
						 *  set  data
						 * @param id
						 */
						set: function (data: any) {
							const self = (this as IECSComponentAttrProxyBase)
							self.__comp[key] = toClonable(data)
						},
					})
					// id
					protoRaw[key] = defaultId
				} else if (clst == StandCustomDataArray && decoInfo instanceof CustomDataArrayDecoInfo) {
					const accessor = decoInfo.accessor
					let { toClonable, toTarget, toTargetArray } = accessor
					Object.defineProperty(proto, key, {
						/**
						 *  get  data id
						 */
						get: function () {
							const self = (this as IECSComponentAttrProxyBase)
							let ids = self.__comp[key] as string[]
							let datas: any
							if (toTargetArray) {
								datas = toTargetArray(ids, self.__comp, key)
							} else {
								datas = ids.map(id => toTarget(id, self.__comp, key))
								Object.defineProperty(datas, "push", {
									value: function (target) {
										if (typeof (target) == "string") {
											self.__comp[key].push(target)
										} else {
											self.__comp[key].push(target.oid)
										}
									}
								})
							}
							return datas
						},
						/**
						 *  set  data
						 * @param id
						 */
						set: function (datas: any[]) {
							const self = (this as IECSComponentAttrProxyBase)
							self.__comp[key] = datas.map(data => toClonable(data))
						},
					})
				} else {
					console.error("unspport type")
				}
			} else {
				Object.defineProperty(proto, key, {
					get: function () {
						return this.__comp[key]
					},
					set: function (value) {
						this.__comp[key] = value
					},
				})
			}
		}
		Object.defineProperty(proto, "setComp", {
			value: function (comp) {
				this.__comp = comp
			}
		})
		Object.defineProperty(proto, "setCompCls", {
			value: function (compClass) {
				this.__compClass = compClass
			}
		})
		Object.defineProperty(proto, "isNull", {
			get: function () {
				return this.__comp == null
			}
		})
		Object.defineProperty(proto, "isNotNull", {
			get: function () {
				return this.__comp != null
			}
		})
		Object.defineProperty(proto, "getOrAdd", {
			value: function () {
				const self = (this as IECSComponentAttrProxyBase)
				if (self.__comp == null) {
					const entityManager = self.__entityManager
					const entity = self.__entity
					const compCls = self.__compClass
					if (!entityManager.existComponent(entity, compCls)) {
						self.__comp = entityManager.addComponent(entity, compCls)
					} else {
						self.__comp = entityManager.getComponent(entity, compCls)
					}
				}
				return this
			}
		})

		target['__ecscompproxyproto'] = proto
	}
}
