namespace fsync {

export class PrefabManager {
	public static readonly inst: PrefabManager = new PrefabManager().init()

	prefabMap: { [key: string]: IPrefab }

	init() {
		this.prefabMap = EmptyTable()
		return this
	}

	loadPrefab(prefabClz: new () => IPrefab) {
		if (this.prefabMap[prefabClz.name]) {
			return this.prefabMap[prefabClz.name]
		}

		let prefab = new prefabClz().init()
		prefab.load()
		this.prefabMap[prefabClz.name] = prefab
		return prefab
	}

	prefabViewMap: { [key: string]: new () => IView } = EmptyTable()
	registerPrefabView(key: string, view: new () => IView) {
		this.prefabViewMap[key] = view
	}

	getPrefabView(key: string): new () => IView {
		return this.prefabViewMap[key]
	}

}

}