namespace fsync {

	export interface IPrefabHelper {
		init()
		instantiate(prefab: IPrefab, depsEnv: ViewPrefabEnv): Entity
	}

	export class PrefabHelper implements IPrefabHelper {
		init() {
			return this
		}

		instantiate(prefab: IPrefab, depsEnv: ViewPrefabEnv): Entity {
			return prefab.create(depsEnv)
		}
	}

	export class PrefabHelperWithoutView implements IPrefabHelper {
		init() {
			return this
		}

		instantiate(prefab: IPrefab, depsEnv: ViewPrefabEnv): Entity {
			return prefab.create(depsEnv)
		}
	}

}