namespace fsync {

	export class PrefabBase implements IPrefab {
		init(): IPrefab {
			return this
		}

		load(): IPrefab {
			return this
		}

		create(depsEnv: ViewPrefabEnv): Entity {
			let entity = this.createEntity(depsEnv)
			let meta = this.getPrefabMeta()
			depsEnv.entityManager.attachComponent(entity, meta)
			// let view = this.createView(depsEnv)
			// depsEnv.viewBinder.bindEntityView(entity, view)
			return entity
		}

		getPrefabMeta(): PrefabMeta {
			throw new Error("Method not implemented.");
		}

		createEntity(depsEnv: ViewPrefabEnv): Entity {
			throw new Error("Method not implemented.");
		}
	}
}