
namespace fsync.ecsproxy {

	/**
	 * entityproxy 
	 * @param target
	 */
	export function DecoEntityProxy<T extends EntityProxyBase>(target: new () => T) {
		proxyClasses.entityProxies.push(target)
	}

	export function DoDecoEntityProxy<T extends EntityProxyBase>(target: new () => T) {
		// 
		let demo = new target()
		let protoRaw = Object.getPrototypeOf(demo)
		let Components = target.prototype['Components'] as (new () => fsync.IComponent)[]
		if (Components == null) {
			protoRaw['Components'] = Components = []
		}
		let dynamicComps = protoRaw["dynamicComps"] as { [key: string]: bool } || {}

		for (let key of Object.keys(demo)) {
			if (demo[key] instanceof CompDecoInfo) {
				const decoInfo = demo[key]
				const cls = decoInfo.cls
				const clst = decoInfo.t
				if (clst == StandCompProxy) {
					Object.defineProperty(protoRaw, key, {
						get: function () {
							const self = (this as EntityProxyBase)
							let entityManager = self.entityManager
							let entity = self.entity
							let proxy = entityProxyHelper.getComponentProxy(cls, entityManager, entity)
							return proxy
						},
						set: function (value: IECSComponentAttrProxyBase) {
							if (value != DeleteMark) {
								throw new Error("unsupport set comp function")
							}
						},
					})
					if (!dynamicComps[key]) {
						Components.push(cls)
					}
				} else {
					console.error("unspport type")
				}
			}
		}

	}
}
