namespace fsync.network.roomclient {
	/**
	 * 
	 */
	export class RoomClientFactory {
		/**
		 * 
		 * - mgobe 
		 * @param techSource 
		 */
		static createClient(techSource: "mgobe" | "glee"): IRoomClient {
			switch (techSource) {
				case "mgobe": {
					let client = new mgobe.RoomClient().init()
					return client
				}
				case "glee": {
					let client = new glee.v1.RoomClient().init()
					return client
				}
				default: {
					throw new Error("invalid techSource")
				}
			}
		}
	}
}
