namespace fsync {

	export class ColliderCastInput {
		target: Entity
		query: IEntityQuery
		entityManager: EntityManager
	}

	export class RayCastInput {
		target: Entity
		query: IEntityQuery
		entityManager: EntityManager
		beginPoint: Vector3
		endPoint: Vector3
	}

	export class CollisionHelper {
		castTargetColliders2D(input: ColliderCastInput, call: (entity1: Entity, entity2: Entity) => any): void {
			const { entityManager, query, target: entity1 } = input
			const entities = query.toArray()
			for (let entity2 of entities) {
				if (entity1 == entity2) {
					continue
				}
				let trans1 = entityManager.getComponent(entity1, Translation)
				let shape1 = entityManager.getComponent(entity1, Shape)
				let trans2 = entityManager.getComponent(entity2, Translation)
				let shape2 = entityManager.getComponent(entity2, Shape)

				let dist = Vector.distance(trans1.value, trans2.value)
				if (dist <= (shape1.radius + shape2.radius)) {
					if (!!call(entity1, entity2)) {
						break
					}
				}
			}
		}

		castTargetWithRay(input: RayCastInput, call: (entity1: Entity, entity2: Entity) => any) {
			const { entityManager, beginPoint, endPoint, query, target: entity1 } = input
			const entities = query.toArray()

			// 
			let p1 = beginPoint
			let p2 = endPoint
			let dirVec = Vector.normalizeSelf(Vector.subDown(p1.clone(), p2))
			// 
			const calcPLD = (pt: Vector3) => {
				let tv = Vector.subDown(p1.clone(), pt)
				return Math.abs(Vector.dot(dirVec, tv))
			}

			// 
			const calcTh = (p0: Vector3, p1: Vector3, p2: Vector3) => {
				let th = Vector.dot(Vector.subDown(p0.clone(), p1), Vector.subDown(p0.clone(), p2))
				return th
			}

			for (let entity2 of entities) {
				if (entity1 == entity2) {
					continue
				}
				let trans1 = entityManager.getComponent(entity1, Translation)
				let shape1 = entityManager.getComponent(entity1, Shape)
				let trans2 = entityManager.getComponent(entity2, Translation)
				let shape2 = entityManager.getComponent(entity2, Shape)

				let distanceMin = shape1.radius + shape2.radius

				// let dist = Vector.distance(trans1.value, trans2.value)
				// if (dist <= (shape1.radius + shape2.radius)) {
				// 	if (call(entity1, entity2)) {
				// 	break
				// }
				// }
				let p3 = trans2.value
				let th1 = calcTh(p1, p2, p3)
				if (th1 < 0) {
					// 
					let distance = Vector.distance(p1, p3)
					if (distance < distanceMin) {
						if (!!call(entity1, entity2)) {
							break
						}
					}
				} else {
					let th2 = calcTh(p2, p1, p3)
					if (th2 < 0) {
						// 
						let distance = Vector.distance(p2, p3)
						if (distance < distanceMin) {
							if (!!call(entity1, entity2)) {
								break
							}
						}
					} else {
						// 
						let D = calcPLD(trans2.value)
						if (D < distanceMin) {
							if (!!call(entity1, entity2)) {
								break
							}
						}
					}
				}
			}
		}
	}

	export const collisionHelper = new CollisionHelper()

}