
namespace fsync.ecsproxy {

	export class EntityProxyQuery<T extends EntityProxyBase> {
		protected entityManager: EntityManager
		protected query: EntityQuery
		protected EntityProxyClass: new () => T

		init(entityManager: EntityManager, EntityProxyClass: new () => T) {
			this.entityManager = entityManager
			this.query = entityManager.createQuery()
			this.EntityProxyClass = EntityProxyClass
			return this
		}

		with(cls: new () => fsync.IComponent) {
			this.query.with(cls)
			return this
		}

		withKey(k: keyof T) {
			return this
		}

		forEach(call: (proxy: T) => void) {
			this.query.forEach((entity) => {
				let proxy = entityProxyHelper.getEntityProxy(this.EntityProxyClass, this.entityManager, entity)
				call(proxy)
			})
		}

		toArray(): T[] {
			return this.query.toArray().map((entity) =>
				entityProxyHelper.getEntityProxy(this.EntityProxyClass, this.entityManager, entity)
			)
		}

		first() {
			return this.toArray()[0]
		}
	}

	export class EntityQueryHelper {
		createQuery<T extends EntityProxyBase>(entityManager: EntityManager, cls: new () => T): EntityProxyQuery<T> {
			let query = new EntityProxyQuery<T>().init(entityManager, cls)
			return query
		}
	}

	export const entityQueryHelper = new EntityQueryHelper()

}
