namespace fsync {

	@cname("Translation")
	@ecsproxy.DecoCompProxy
	export class Translation implements IComponent {
		value: Vector3 = new Vector3()
	}

	@cname("Scale")
	@ecsproxy.DecoCompProxy
	export class Scale implements IComponent {
		value: Vector3 = new Vector3(1)
	}

	@cname("Rotation")
	@ecsproxy.DecoCompProxy
	export class Rotation implements IComponent {
		value: Vector4 = Vector4.fromNumArray([0, 0, 0, 1])
	}

}