namespace fsync {

	@cname("TransformSyncSystem")
	export class TransformSyncSystem extends SystemBase {
		viewBinder: ViewBindManager
		update() {

			// entity
			// !!! entityidview
			{
				let entities = this.entityManager.createQuery().toArray()
				let existsMap = EmptyTable()
				for (let entity of entities) {
					existsMap[entity.identity] = entity
				}
				let viewMap = EmptyTable()
				for (let id of this.viewBinder.getAllEntityID()) {
					viewMap[id] = true
					if (!existsMap[id]) {
						this.viewBinder.removeEntityById(id)
					}
				}
				for (let entity of entities) {
					if (!viewMap[entity.identity]) {
						let meta = this.entityManager.getComponent(entity, PrefabMeta)
						if (meta != null) {
							let ViewFactory = PrefabManager.inst.getPrefabView(meta.prefabId)
							let view = new ViewFactory().init()
							this.viewBinder.bindEntityView(entity, view)
						}
					}
				}
			}

			// console.log("allentitycount:", this.entityManager.createQuery().with(Translation).toArray().length)
			this.entityManager.createQuery().with(Translation).forEach((entity) => {
				let view = this.viewBinder.getEntityView(entity)
				if (view != null) {
					let trans = this.entityManager.getComponent(entity, Translation)
					view.setPos(trans.value)
				}
			})
			this.entityManager.createQuery().with(Scale).forEach((entity) => {
				let view = this.viewBinder.getEntityView(entity)
				if (view != null) {
					let scale = this.entityManager.getComponent(entity, Scale)
					view.setScale(scale.value)
				}
			})
			this.entityManager.createQuery().with(Rotation).forEach((entity) => {
				let view = this.viewBinder.getEntityView(entity)
				if (view != null) {
					let rotate = this.entityManager.getComponent(entity, Rotation)
					view.setRotation(rotate.value)
				}
			})
		}
	}

}