

namespace fsync.eds {

	function buildGetSet(target: Object, property: string, dataManager: DataManager) {
		const value0 = target[property]
		const hiddenProp = "__" + property + "__"

		delete target[property]
		if (!(value0 instanceof DataClassDef)) {
			target[hiddenProp] = value0
			Object.defineProperty(target, property, {
				get: function () {
					return this[hiddenProp]
				},
				set: function (value) {
					let desc = Object.getOwnPropertyDescriptor(this, hiddenProp)
					if (!desc.writable) {
						throw new Error("cannot set value to a prop not writable")
					}
					if (this[hiddenProp] != value) {
						this[hiddenProp] = value
						this.dataManager.dirtyManager.markDirty(this)
					}
				}
			});
		} else {
			if (value0 == _NullData) {
				target[hiddenProp] = ""
			} else {
				let ctype = value0.t
				let subData = dataManager.addData(ctype)
				target[hiddenProp] = subData.oid
			}
			Object.defineProperty(target, property, {
				get: function () {
					let oid = this[hiddenProp]
					let data = this.dataManager.getDataById(oid)
					return data
				},
				set: function (value) {
					let targetOid = value.oid
					if (this[hiddenProp] != targetOid) {
						this[hiddenProp] = targetOid
						this.dataManager.dirtyManager.markDirty(this)
					}
				}
			});
		}
	}

	export function DefindECSDataMetaValue(data: IDataClass$, dataManager: DataManager) {
		let uidTool = dataManager.utils.uidTool

		const typeName = data["constructor"].name
		Object.defineProperty(data, 'otype', {
			value: typeName,
			writable: false,
			enumerable: false,
		})
		Object.defineProperty(data, 'oid', {
			value: uidTool.genTypedId(typeName),
			writable: false,
			enumerable: false,
		})
		Object.defineProperty(data, 'dataManager', {
			value: dataManager,
			writable: false,
			enumerable: false,
		})
	}

	export function DecoECSDataClass(data: IDataClass$, dataManager: DataManager) {
		if (data.oid != null) {
			return
		}

		for (let key in data) {
			if (data.hasOwnProperty(key)) {
				buildGetSet(data, key, dataManager);
			}
		}

		DefindECSDataMetaValue(data, dataManager)
	}

	/**
	 *  ecs : ecsdata -> copy
	 * @param ecsdata 
	 * @param copy 
	 * @param dataManager 
	 */
	export function MergeECSData(ecsdata: IDataClass$, copy: IDataClass$, dataManager: DataManager): void {

		if ((copy as IMerge<any>).mergeFrom) {
			//   merge, merge
			(copy as IMerge<any>).mergeFrom(ecsdata)
		} else {
			// 
			for (let key in ecsdata) {
				const data = ecsdata[key]
				if (typeof (data) == "object" && data != null) {
					if ((data as IMerge<any>).mergeFrom
						&& (!!copy[key])
						&& Object.getPrototypeOf(copy[key]) == Object.getPrototypeOf(data)
					) {
						(copy[key] as IMerge<any>).mergeFrom(data);
					} else if ((data as IClone).clone != undefined) {
						const copyAttrValue = copy[key] = (data as IClone).clone() as IDataClass$
						DefindECSDataMetaValue(copyAttrValue, dataManager)
					} else {
						if (data instanceof Array) {
							// 
							copy[key] = [...data]
						} else {
							throw new Error("unsupport data type to clone")
						}
					}
				} else {
					copy[key] = data
				}
			}

			if (copy.oid == null) {
				for (let key of Object.getOwnPropertyNames(ecsdata)) {
					if (!key.startsWith("__") && key != "dataManager") {
						let prop = Object.getOwnPropertyDescriptor(ecsdata, key)
						Object.defineProperty(copy, key, prop)
					}
				}

				delete copy["dataManager"]
				Object.defineProperty(copy, 'dataManager', {
					value: dataManager,
					writable: false,
					enumerable: false,
				})

			}

			// 
			Object.setPrototypeOf(copy, Object.getPrototypeOf(ecsdata))
		}

	}

	export function CloneECSData(ecsdata: IDataClass$, dataManager: DataManager): IDataClass$ {
		if ((ecsdata as IClone).clone) {
			//   clone, clone
			let copy = (ecsdata as IClone).clone() as IDataClass$
			return copy
		} else {
			let copy: IDataClass$ = {
				// oid: ecsdata.oid,
				// otype: ecsdata.otype,
			}

			MergeECSData(ecsdata, copy, dataManager)

			return copy
		}
	}

}
