
namespace kitten.rpg {
	export type TActorId = string

	export class RPGPlayerCmd implements fsync.IGameInputCmd {
		netFrameCount?: number;
		isAdjustedForSurge?: boolean;
		cmdType: "RoleCmd" | "Pass";
		cmdId: string;
		needSync?: boolean;
		route: "net" | "local";
		roleId: fsync.TRoleId;
		createTime: number;
		genType?: "con" | "sep";
		createFrameCount?: number;
		frameCount?: number;
		receivedTime?: number;
		isSurge?: boolean;
		cmdIndex: number;
		move?: {
			// id
			actorId: TActorId
			times: number,
			// 
			dir: number[];
		};
		skills?: {
			// id
			actorId: TActorId
			skillName: string;
			skillId: number;
			/**
			 * 
			 */
			skillIndex: number;

			touchAction: gamepad.TTouchAction
			targets?: string[];
			// 
			dir: number[];
		}[]

	}
}
