namespace fsync {

	/**
	 * entity
	 */
	export class EntityMergeSystem implements IUpdater {
		// merge from source to target
		source: ECSWorld
		target: ECSWorld

		// 
		needMerge: bool = false

		init() {
			this.needMerge = false
			return this
		}

		// mg1 -> mg2
		protected mergeDiffEntities(mg1: EntityManager, mg2: EntityManager) {
			mg1.dirtyManager.sortDirtyEntities(mg1)
			mg2.dirtyManager.sortDirtyEntities(mg2)

			let applyMap: { [key: string]: bool } = EmptyTable()

			type EntityMap = { [key: string]: Entity }
			let newMap: EntityMap = EmptyTable()
			let removeMap: EntityMap = EmptyTable()
			let modifyMap: EntityMap = EmptyTable()

			mg1.dirtyManager.forEachDirtyEntities((entity) => {
				if (applyMap[entity.identity]) {
					return
				}
				applyMap[entity.identity] = true

				if (mg2.existsEntity(entity)) {
					if (mg1.existsEntity(entity)) {
						// this.onEntityUpdate(entity)
						modifyMap[entity.identity] = entity
					} else {
						// this.onRemoveEntity(entity)
						removeMap[entity.identity] = entity
					}
				} else {
					if (mg1.existsEntity(entity)) {
						// this.onNewEntity(entity)
						newMap[entity.identity] = entity
					}
				}
			})

			mg2.dirtyManager.forEachDirtyEntities((entity) => {
				if (applyMap[entity.identity]) {
					return
				}
				applyMap[entity.identity] = true

				if (mg1.existsEntity(entity)) {
					if (mg2.existsEntity(entity)) {
						// this.onEntityUpdate(entity)
						modifyMap[entity.identity] = entity
					} else {
						// this.onNewEntity(entity)
						newMap[entity.identity] = entity
					}
				} else {
					if (mg2.existsEntity(entity)) {
						// this.onRemoveEntity(entity)
						removeMap[entity.identity] = entity
					}
				}
			})

			// for (let entity of Object.values(removeMap)) {
			// 	if (newMap[entity.identity]) {
			// 		console.log("lkjwel")
			// 	}
			// }

			for (let entity of Object.values(removeMap)) {
				this.onRemoveEntity(entity)
			}

			for (let entity of Object.values(newMap)) {
				this.onNewEntity(entity)
			}

			for (let entity of Object.values(modifyMap)) {
				this.onUpdateEntity(entity)
			}

			mg1.dirtyManager.clearFlags()
			mg2.dirtyManager.clearFlags()
		}

		// entity
		onNewEntity(entity: Entity) {

		}

		// entity
		onRemoveEntity(entity: Entity) {

		}

		// entity
		onUpdateEntity(entity: Entity) {

		}

		onBeforeUpdate(): void {
			try {
				if (this.needMerge) {
					this.mergeDiffEntities(this.source.entityManager, this.target.entityManager)
					this.target.mergeFrom(this.source)
				}
			} catch (e) {
				console.error(e)
			}
		}
		onAfterUpdate(): void {
		}

		update() {

		}
	}

}