namespace fsync {

	export interface IEntityQuery {
		forEach(call: (entity: Entity) => void): IEntityQuery
		toArray(): Entity[]
		first(): Entity
		count(): number
	}

	export class EntityQuery implements IEntityQuery {
		entityManager: EntityManager
		componentTypes: ComponentType[] = []
		withoutComponentTypes: ComponentType[] = []
		init(entityManager: EntityManager): EntityQuery {
			this.entityManager = entityManager
			return this
		}

		with(componentType: ComponentType | (new () => IComponent)): EntityQuery {
			if (typeof (componentType) == "string") {
				this.componentTypes.push(componentType)
			} else {
				this.componentTypes.push(componentType.name)
			}
			return this
		}

		without(componentType: ComponentType | (new () => IComponent)): EntityQuery {
			if (typeof (componentType) == "string") {
				this.withoutComponentTypes.push(componentType)
			} else {
				this.withoutComponentTypes.push(componentType.name)
			}
			return this
		}

		forEach(call: (entity: Entity) => void): EntityQuery {
			const entityManager = this.entityManager
			entityManager.entityContainer.forEachEntities(this.componentTypes, this.withoutComponentTypes, call)
			return this
		}

		forEachWithComps(call: (entity: Entity, ...comps: fsync.IComponent[]) => void): EntityQuery {
			const entityManager = this.entityManager
			entityManager.entityContainer.forEachEntities(this.componentTypes, this.withoutComponentTypes, (entity) => {
				let comps = this.componentTypes.map(t => entityManager.getComponentByType(entity, t))
				call(entity, ...comps)
			})
			return this
		}

		toArray(): Entity[] {
			let entities: Entity[] = []
			this.forEach((entity) => {
				entities.push(entity)
			})
			return entities
		}

		first(): Entity {
			let first: Entity
			this.forEach((entity) => {
				first = entity
				return true
			})
			return first
		}

		count(): number {
			let count = 0
			this.forEach((entity) => {
				count++
			})
			return count
		}
	}

}