namespace fsync {

	export class CmdReorderQueue {
		protected curCmdIndex: number = 0

		protected cmds: IGameInputCmd[]

		init() {
			this.cmds = []
			return this
		}

		protected dirty: bool = false

		put(cmd: IGameInputCmd): void {
			this.dirty = true

			this.cmds.push(cmd)
		}

		sort(): void {
			this.cmds.sort((a, b) => a.cmdIndex - b.cmdIndex)
		}

		pop(): IGameInputCmd {
			if (this.dirty) {
				this.dirty = false
				this.sort()
			}

			if (this.cmds.length > 0) {
				let cmd = this.cmds[0]
				if (cmd.cmdIndex == this.curCmdIndex + 1) {
					this.curCmdIndex++
					this.cmds.shift()
					return cmd
				}
			}

			return null
		}

	}

	export class NetworkCmdTranslator {
		textDecorder: TextDecoder = new TextDecoder("utf-8")

		translate(message: roomserver.TReqBroadCastFrameSyncReq): IGameInputCmd {
			let sdata = this.textDecorder.decode(message.msgBytes)
			let data = JSON.parse(sdata) as IGameInputCmd
			// console.log(`received net cmd: ${JSON.stringify(data)}`)
			data.route = "net"
			data.frameCount = LongHelper.toNumber(message.syncInfo.serverFrameCount)
			data.receivedTime = Date.now()
			return data
		}

	}

}